import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import { getPageData, resetPageStore, updatePageData } from '../actions/page';
import { Endpoint } from '../utils/constants';
import { dateToStr, exportToCsv } from '../utils/formatter';
import Loader from '../components/customFields/loader';
import NoRecord from '../components/customFields/noRecord';
import InputField from '../components/customFields/inputField';
import ModalButton from '../components/modal/button';


class Qguests extends Component {
    constructor(props) {
        super(props);
        this.keymapper = {
            header: ["DATE", "TIME", "MEMBER'S NAME", 'MEMBER NO.', "GUEST'S NAME", "PHONE NO.", "WITH", "CHECKED IN"],
            keys: ["inviteDate", "inviteTime", "mName", "mno", "gName", "phone", "withGuest", "status"],
            mapper: { status: { 1: 'YES', 2: 'NO' } }
        }
        this.state = {
            date: dateToStr(null, 'YYYY-MM-DD'),
            isinit: true,
            list: [],
            totalCount: ''
        };
    }

    componentDidMount() {
        this.getList();
    }

    componentWillUnmount() {
        this.props.resetPageStore('pageData');
    }

    getList = () => {
        this.props.getPageData({
            params: { token: this.props.token, date: this.state.date },
            url: Endpoint.CHECKIN_GUEST
        })
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.list != prevState.list && !nextProps.isLoader) {
            return { list: nextProps.list, totalCount: nextProps.count, isinit: false }
        }
        return null
    }

    loadData = (e) => {
        let { value, name } = e.target;
        this.setState({ date: value }, () => {
            this.getList();
        });
    }

    changeStatus = (e, item, status) => {
        e.preventDefault();
        let payload = { token: this.props.token, id: item.id,  type:'status', status: status === 1 ? 2 : 1};
        this.props.updatePageData({
            payload,
            url: `${Endpoint.CHECKIN_GUEST}/${item.id}`,
            method: 'PUT'
        }, (response) => {
            if (response.status) {
                this.getList();
                this.setState({ error: '' });
                //this.props.toastmessage(response.message);
            } else {
                this.setState({ error: response.message })
            }
        });
    }

    delete = (e, closeModal, item) => {
        e.preventDefault();
        let params = { token: this.props.token};
        this.props.updatePageData({
            params,
            url: `${Endpoint.CHECKIN_GUEST}/${item.id}`,
            method: 'DELETE'
        }, (response) => {
            if (response.status) {
                closeModal();
                this.getList();
                this.setState({ error: '' });
                //this.props.toastmessage(response.message);
            } else {
                this.setState({ error: response.message })
            }
        });
    }

    render() {
        let { list, date } = this.state;
        let { isLoader } = this.props;
        return (
            <div className='makeReservation'>
                <div className='row'>
                    <div className='col-6'><div className='_title'>GUESTS AT THE Q</div></div>
                    <div className='col-6'>
                        <div className='d-flex justify-content-end'>
                            <div className='select-date'>
                                <InputField name="date" value={date} inputChange={this.loadData} dom={{ placeholder: 'Select Date', type: 'date', dateFormat: "dd/MM/yyyy" }} />
                            </div>
                            <div className='btn mr10 ml10' onClick={() => exportToCsv(list, 'checked_in_member', this.keymapper)}>Download Report</div>
                            <Link className='btn w150' to='/invite-guests'>Invite Guest</Link>
                        </div>
                    </div>
                </div>
                <div className='reservation-list'>
                    <div className='row no-gutters'>
                        <div className='col list-title'>DATE</div>
                        <div className='col list-title'>TIME</div>
                        <div className='col list-title'>MEMBER'S NAME</div>
                        <div className='col list-title'>M. NO.</div>
                        <div className='col list-title'>GUEST'S NAME</div>
                        <div className='col list-title'>PHONE NO.</div>
                        <div className='col list-title text-center'>WITH</div>
                        <div className='col list-title text-center'>CHECKED IN</div>
                        <div className='col list-title'></div>
                    </div>
                    {isLoader && <Loader />}
                    {!isLoader && (!list || list.length == 0) && <NoRecord />}
                    <div className='listScroll'>
                        {!isLoader && list && list.length > 0 &&
                            list.map((item, i) => {
                                return (
                                    <div className='row no-gutters' key={i}>
                                        <div className='col'>
                                            <div className='list-data'>{item.inviteDate}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data'>{item.inviteTime}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data'>{item.mName}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data'>{item.mno || '--'}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data'>{item.gName}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data'>{item.phone}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data text-center'>+{item.withGuest}</div>
                                        </div>
                                        <div className='col'>
                                            <div className='list-data text-center'>{item.status == 1 ? 'YES' : 'NO'}</div>
                                        </div>
                                        <div className="col">
                                            <span className='ml20 cursor-pointer color-green fs20' onClick={(e) => this.changeStatus(e, item, item.status)}><i className='fa fa-check-circle'></i></span>
                                            {/* <Link className='ml20 cursor-pointer color-white' to={`/invite-guests/${item.id}`}><i className='fa fa-edit'></i></Link> */}
                                            <ModalButton
                                                    className="ml20 cursor-pointer color-red fs18"
                                                    tag="span"
                                                    data={{
                                                        showCustomModal: true,
                                                        id: item.id,
                                                        title: '',
                                                        footer: true,
                                                        onSubmit: this.delete,
                                                        submitArg: {id: item.id},
                                                        body: 'Are you sure want to delete it',
                                                        className: ''
                                                    }}
                                                >
                                                    <i className='fa fa-remove'></i>
                                                </ModalButton>
                                            {/* <span className='ml20 cursor-pointer color-red fs18' onClick={(e) => this.delete(e, item)}><i className='fa fa-remove'></i></span> */}
                                        </div>
                                    </div>)
                            })
                        }
                    </div>
                </div>
            </div>
        );
    }
} function mapStateToProps({ auth: { token }, page: { pageData: { data = [], count = 0 }, updatedData = {}, isLoader = false } }) {
    return { token, isLoader, list: data, count, updatedData }
}
export default connect(mapStateToProps, { getPageData, resetPageStore, updatePageData })(Qguests);
