import React, { Component } from 'react';
import { connect } from 'react-redux';

import InputField from '../customFields/inputField';
import Chips from '../customFields/chips';
import Loader from '../customFields/loader';
import { getValue, dateToStr } from '../../utils/formatter';
import { validate } from '../../utils/validation';
import { isObject, isObjEmpty } from '../../utils';
import { updatePageData, resetPageStore, getIndividualItem } from '../../actions/page';
import { Endpoint } from '../../utils/constants';
import '../../style/newStyle.scss';

class BookReservation extends Component {
  constructor(props) {
    super(props);
    this.state = this.intialiseState();
  }
  intialiseState = (data = {}, isinit = true, rooms={},  rTypeId='') => {
    return {
      memberId: getValue(data, 'memberId', ''),
      memberId_name: data.memberObj ? [data.memberObj] : [],
      reservationType: getValue(data, 'nature', ''),
      rTypeId: getValue(data, 'rTypeId', rTypeId),
      startTime: getValue(data, 'startTime', null),
      endTime: getValue(data, 'endTime', null),
      date: getValue(data, 'date', dateToStr(null, 'YYYY-MM-DD')),
      guest: getValue(data, 'guest', ''),
      errorMessages: {},
      isValid: {},
      rooms,
      isinit
    }
  }

  componentDidMount() {
    const { rTypeId, selectedRoom, date, bookingId='' } = this.props;
    let rooms = {};
    if (selectedRoom.child && selectedRoom.child.length) {
      selectedRoom.child.forEach((room) => {
        rooms[room.id] = room.type;
      })
    }
    this.setState({ rTypeId, rooms, date, bookingId });
    if (bookingId) {
      this.props.getIndividualItem({
        params: { token: this.props.token },
        url: `${Endpoint.RESERVATION}/${bookingId}`
      });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.itemData != this.props.itemData && (prevState.isinit) && isObject(this.props.itemData) && !isObjEmpty(this.props.itemData)) {
      let stateD = this.intialiseState(this.props.itemData, false, this.state.rooms, this.state.rTypeId);
      this.setState({...stateD, bookingId: this.props.bookingId || ''});
    }
    if(prevState.bookingId != this.props.bookingId && !this.state.isinit){
      this.setState({isinit :true})
      this.props.getIndividualItem({
        params: { token: this.props.token },
        url: `${Endpoint.RESERVATION}/${this.props.bookingId}`
      });
    }
  }

  componentWillUnmount() {
    this.props.resetPageStore('individualItem');
  }

  validate = (name, value) => {
    let { isValid, errorMessages } = this.state;
    let error = validate(name, value, { type: 'text', errorMsg: name == 'memberId' ? 'No member selected' : '' });
    isValid[name] = error.isValid;
    error.isValid
      ? delete errorMessages[name]
      : (errorMessages[name] = error.errorMsg);
    this.setState({ isValid, errorMessages });
  };

  inputChange = e => {
    let {
      target: { name, value, checked, type }
    } = e;
    this.setState({ [name]: type === 'checkbox' ? checked : value }, () => {
      if ((name === 'rTypeId' || name === 'date') && this.state.rTypeId && this.state.date) {
        this.props.refreshList(this.state.rTypeId, this.state.date);
      }
      //this.validate(name, value);
    });
  };
  compareStartEndTime = () => {
    let { startTime, endTime, error } = this.state;
    let s = new Date("1970-01-01 8:00 am").getTime();
    let st = new Date("1970-01-01 " + startTime).getTime();
    let et = new Date("1970-01-01 " + endTime).getTime();
    if (st < s) {
      error = "Start time must be greater than or equal to 08:00 am";
    } else if (st > et) {
      error = "Start time must be less than end time";
    } else {
      error = ''
    }
    this.setState({ error });
    return error ? false : true;
  }
  onSubmit = (e) => {
    e.preventDefault();
    ["memberId", "rTypeId", "startTime", "guest", 'date'].forEach((name) => {
      this.validate(name, this.state[name]);
    });
    let isvalid = this.compareStartEndTime();
    if (Object.keys(this.state.errorMessages).length === 0 && isvalid) {
      this.setState({ error: '', isSaving: true });
      const { memberId, rTypeId, startTime, endTime, date, guest } = this.state;
      let payload = { memberId, rTypeId, startTime, endTime, date, guest, token: this.props.token };
      this.props.updatePageData({
        payload,
        url: `${Endpoint.RESERVATION}${this.props.bookingId ? '/' + this.props.bookingId : ''}`,
        method: this.props.bookingId ? "PUT" : 'POST',
        action: this.props.bookingId ? "update" : 'add',
        page: 'reserve',
      }, (response) => {
        if (response.status) {
          this.setState({ error: '', isSaving: false });
          //this.props.toastmessage(response.message);
          this.props.goBack();
        } else {
          this.setState({ error: response.message, isSaving: false })
        }
      });
    }
  }
  setChips = (value, name, item = {}) => {
    this.setState({ [name]: item.id, [name + '_name']: value });
    let { isValid, errorMessages } = this.state;
    isValid[name] = value.length ? true : false;
    value.length ? delete errorMessages[name] : errorMessages[name] = 'This field is required.';
    this.setState({ errorMessages, isValid })
  };
  render() {
    const { rooms, memberId_name, rTypeId, startTime, endTime, date, guest, errorMessages, isValid, error, isSaving } = this.state;
    const { token, selectedRoom, itemLoader, bookingId } = this.props;
    return (
      <div className="col-md-6">
        {itemLoader && <Loader />}
        {!itemLoader && (
          <React.Fragment>
            <div className="row no-gutters">
              <div className="col-md-4">
                <div className="tabs active">
                  <p className="">{selectedRoom.type}</p>
                </div>
              </div>
              <div className="col text-right">
                <div className="backBtn" onClick={this.props.goBack}>
                  Back
                    </div>
              </div>
            </div>
            <div className="checkedin_data">
              <div className="d-flex">
                <div className="mr10 w360">
                  <p>Date</p>
                  <InputField
                    name="date"
                    groupClass="mr10"
                    value={date}
                    isValid={isValid.date}
                    errorMsg={errorMessages.date}
                    inputChange={this.inputChange}
                    dom={{
                      placeholder: "dd/mm/yyyy",
                      type: "date",
                      minDate: new Date(),
                      dateFormat: "dd/MM/yyyy"
                    }}
                  />
                </div>
                <div>
                  <p>Time</p>
                  <div className="d-flex">
                    <div>
                    <InputField
                      name="startTime"
                      groupClass="mr10"
                      value={startTime}
                      isValid={isValid.startTime}
                      errorMsg={errorMessages.startTime}
                      inputChange={this.inputChange}
                      dom={{ placeholder: "hh:mm", type: "time" }}
                    /></div>
                    <InputField
                      name="endTime"
                      value={endTime}
                      inputChange={this.inputChange}
                      dom={{ placeholder: "hh:mm", type: "time" }}
                    />
                  </div>
                </div>
              </div>
              {selectedRoom.child && selectedRoom.child.length > 0 && (
                <div>
                  <p>Select Room</p>
                  <InputField
                    name="rTypeId"
                    groupClass="togglebtn"
                    value={rTypeId}
                    inputChange={this.inputChange}
                    dom={{ type: "toggle" }}
                    options={rooms}
                  />
                </div>
              )}
              <div>
                <p>Member Details</p>
                <Chips
                  className="checkIn-search"
                  placeholder="Search by First Name, Last Name or Member ID"
                  name="memberId"
                  namespace="countryList"
                  livesearch={true}
                  autocomplete={false}
                  isSingle={true}
                  service={`${Endpoint.MEMBER}?token=${token}`}
                  vk="id"
                  sk="name"
                  chips={memberId_name}
                  maxCount={1}
                  image="image"
                  stamp="mno"
                  isHighlight={true}
                  setChips={this.setChips}
                />
              </div>
              <div className="d-flex">
                <div className="w100per">
                  <p>Number Of Guests</p>
                  <InputField
                    name="guest"
                    groupClass="text-right mb0"
                    value={guest}
                    isValid={isValid.guest}
                    errorMsg={errorMessages.guest}
                    inputChange={this.inputChange}
                    dom={{ placeholder: "0", type: "number" }}
                  />
                </div>
                <div
                  onClick={this.onSubmit}
                  className={`btn w100per txt_break  d-flex align-items-center justify-content-center ${isSaving ? "btndisabled" : ""}`}>
                  {bookingId ? 'Update Reservation' : 'Make a Reservation'}
                </div>
              </div>
              {error && <p className="error_msg"> {error}</p>}
            </div>
          </React.Fragment>
        )}
      </div>
    );
  }
}
function mapStateToProps({ auth: { token }, page: { individualItem: { data = {} }, itemLoader = false } }) {
  return { token, itemData: data, itemLoader }
}

export default connect(mapStateToProps, { updatePageData, getIndividualItem, resetPageStore })(BookReservation);
