import React, { Component } from 'react';
import { connect } from 'react-redux';

import { getPageData, fetchSuggestions, updatePageData, resetPageStore } from '../actions/page';
import { Endpoint } from '../utils/constants';
import { dateToStr, exportToCsv } from '../utils/formatter';
import Loader from '../components/customFields/loader';
import NoRecord from '../components/customFields/noRecord';
import BookReservation from '../components/reservation/bookReservation';
import Filters from '../components/reservation/filter';
import InputField from '../components/customFields/inputField';
import "../style/newStyle.scss";
// const STATUS = { 1: 'Approved', 2: 'Disapproved', 3: 'Pending' };

class Reservation extends Component {
    constructor(props) {
        super(props);
        this.current = 1;
        this.list = [];
        this.keymapper = {
            header: ["Reservation", "Member Name", "Guest", 'Area', 'Status'],
            keys: [["startTime", "endTime"], "name", "guest", "type", "status"],
            mapper: { status: { 1: 'Approved', 2: 'Disapproved', 3: 'Pending' } }
        }
        this.state = {
            rtypeId: '',
            isinit: true,
            list: [],
            error: '',
            selectedRoom: null,
            bookingId: '',
            date: dateToStr(null, 'YYYY-MM-DD'),
            isFilter: false
        }
    }

    componentDidMount() {
        this.props.fetchSuggestions({ url: Endpoint.RESERVATION_TYPE, params: { token: this.props.token }, type: 'groupReservationType' });
        this.props.fetchFilterCat({ url: Endpoint.RESERVATION_TYPE, params: { token: this.props.token, type: 'all' }, type: 'combo', key: 'id', value: 'type' });
        this.getList();
    }

    componentWillUnmount() {
        this.props.resetPageStore('pageData');
    }

    getList = (otherData = {}) => {
        this.props.getPageData({
            params: { token: this.props.token, rtypeId: this.state.rtypeId, date: this.state.date, ...otherData },
            url: Endpoint.RESERVATION_LIST
        })
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.list != prevState.list && prevState.isinit && nextProps.isLoader) {
            return { list: nextProps.list, totalCount: nextProps.count, isinit: false }
        }
        return null
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.list != prevProps.list) {
            this.setState({ list: this.props.list, totalCount: this.props.count });
        }
    }

    setRoom = (selectedRoom) => {
        this.setState({ selectedRoom, rtypeId: selectedRoom.id, bookingId: '' }, () => {
            this.getList();
        })
    }

    refreshList = (rtypeId, date, otherData = {}) => {
        this.setState({ rtypeId, date }, () => {
            this.getList(otherData);
        })
    }

    goBack = () => {
        this.current = 1;
        this.setState({ selectedRoom: null, rtypeId: '', date: dateToStr(null, 'YYYY-MM-DD'), isFilter: false }, () => {
            this.getList();
        })
    }

    edit = (item) => {
        let { reservationTypeList } = this.props;
        let group = reservationTypeList ? Object.keys(reservationTypeList) : [];
        let selectedRoom = null;
        for (let i = 0; i < group.length; i++) {
            let s = { ...reservationTypeList[i] };
            if (s.groupName === item.groupName) {
                selectedRoom = s;
                break;
            }
        }
        this.setState({ selectedRoom, rtypeId: item.rTypeId, bookingId: item.id }, () => {
            this.getList();
        })
    }

    changeStatus = (e, status, item) => {
        e.preventDefault();
        let payload = { token: this.props.token, status, iid: item.id };
        this.props.updatePageData({
            payload,
            url: `${Endpoint.RESERVATION_LIST}/${item.id}`,
            method: 'PUT'
        }, (response) => {
            if (response.status) {
                this.getList();
                this.setState({ error: '' });
                //this.props.toastmessage(response.message);
            } else {
                this.setState({ error: response.message })
            }
        });
    }

    goToFilters = (e) => {
        e.preventDefault();
        this.setState({ isFilter: true });
    }

    loadData = (e) => {
        let { value, name } = e.target;
        this.setState({ date: value }, () => {
          this.getList();
        });
      }

    render() {
        const { list, selectedRoom, rtypeId, date, isinit, bookingId, isFilter } = this.state;
        let { reservationTypeList, isLoader } = this.props;
        let group = reservationTypeList ? Object.keys(reservationTypeList) : [];
        this.current = 1;
        return (
            <div className='quorum_new'>
                {isinit && <Loader />}
                {!isinit &&
                    <div className={`reservation ${selectedRoom || isFilter ? 'checkedin' : ''}`}>
                        <div className='row'>
                            {!selectedRoom && !isFilter && <div className='col-md-6'>
                                <div className='tab-scroll'>
                                    <div className='d-flex flex-wrap'>
                                        {group && group.length > 0 && group.map((gr, i) => {
                                            let room = reservationTypeList[gr];
                                            let c = this.current;
                                            this.current = this.current === i ? this.current + 3 : this.current;
                                            return <div key={i} className={`tabs tab-square`} onClick={() => this.setRoom(room)}><p>{room.type}</p></div>
                                        })}
                                    </div>
                                    {group && group.length > 0 &&
                                        <div className='search_tab' onClick={this.goToFilters}>
                                            <div className='tabs'><p>Search a Reservation</p></div>
                                        </div>
                                    }
                                </div>
                            </div>
                            }
                            {selectedRoom && Object.keys(selectedRoom).length > 0 &&
                                <BookReservation
                                    bookingId={bookingId}
                                    goBack={this.goBack}
                                    refreshList={this.refreshList}
                                    selectedRoom={selectedRoom} rTypeId={rtypeId} date={date} />
                            }
                            {isFilter &&
                                <Filters
                                    goBack={this.goBack}
                                    refreshList={this.refreshList} />
                            }
                            <div className='col-md'>

                                {list && list.length > 0 && <div className='d-flex align-items-center justify-content-between'>
                                    <div className='color-white'></div>
                                    <div className='d-flex'>
                                        <div className='select-date'>
                                            <InputField name="date" value={date} inputChange={this.loadData} dom={{ placeholder: 'Select Date', type: 'date', dateFormat: "dd/MM/yyyy" }} />
                                        </div>
                                        <div className='btn ml10' onClick={() => exportToCsv(list, 'reservation_list', this.keymapper)}>Download Report</div></div>
                                </div>}
                                {!isLoader && (!list || list.length === 0) && (
                                    <NoRecord message="No reservations found" />
                                )}
                                {isLoader && <Loader />}
                                {!isLoader && list && list.length > 0 && (
                                    <div className='table_scroll'>
                                        <table>
                                            <thead>
                                                <tr className='text-center'>
                                                    <th width="22%">Reservation (Time/Date)</th>
                                                    <th>Member Name</th>
                                                    <th width="2%">Guests</th>
                                                    <th>Area</th>
                                                    <th>Actions</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {list.map((item, i) => {
                                                    return (
                                                        <tr key={i} style={{ color: item.status == 1 ? '#27ce27' : item.status == 2 ? '#fd2b22' : '' }} className='text-center'>
                                                            <td>{dateToStr(item.startTime, 'H:mm')} - {dateToStr(item.endTime, 'H:mm')} {dateToStr(item.startTime, 'DD/MM/YYYY')}</td>
                                                            <td>{item.name}</td>
                                                            <td className="text-center">{item.guest}</td>
                                                            <td>{item.type}</td>
                                                            <td>
                                                                {(item.status == 3 || item.status == 2) && <span className='ml20 cursor-pointer color-green fs20' onClick={(e) => this.changeStatus(e, 1, item)}><i className='fa fa-check-circle'></i></span>}
                                                                <span className='ml20 cursor-pointer color-white' onClick={() => this.edit(item)}><i className='fa fa-edit'></i></span>
                                                                {(item.status == 3 || item.status == 1) && <span className='ml20 cursor-pointer color-red fs18' onClick={(e) => this.changeStatus(e, 2, item)}><i className='fa fa-ban'></i></span>}
                                                            </td>
                                                        </tr>
                                                    );
                                                })}
                                            </tbody>
                                        </table>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                }
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: { pageData: { data = [], count = 0 }, reservationTypeList = {}, isLoader = false } }) {
    return { token, isLoader, list: data, count, reservationTypeList }
}

const mapDispatchToProps = function (dispatch, ownProps) {
    return {
        fetchSuggestions: myParam => dispatch(fetchSuggestions(myParam, 'reservationTypeList')),
        fetchFilterCat: myParam => dispatch(fetchSuggestions(myParam, 'suggesions')),
        getPageData: myParam => dispatch(getPageData(myParam)),
        resetPageStore: myParam => dispatch(resetPageStore(myParam)),
        updatePageData: (myParam, cb) => dispatch(updatePageData(myParam, cb))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Reservation);
