import React, { Component } from 'react';
import { connect } from 'react-redux';
import ModalButton from '../components/modal/button';

import { getPageData, resetPageStore } from '../actions/page';
import { Endpoint } from '../utils/constants';
import Loader from '../components/customFields/loader';
import NoRecord from '../components/customFields/noRecord';

class IndiaofIdeas extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isinit: true,
            list: [],
            totalCount: ''
        };
    }
    componentDidMount() {
        this.getList();
    }
    componentWillUnmount(){
        this.props.resetPageStore('pageData');
    }

    getList = () => {
        this.props.getPageData({
            params: { token: this.props.token },
            url: Endpoint.IDEAS_MANAGE
        })
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.list != prevState.list && prevState.isinit && nextProps.list.length) {
            return { list: nextProps.list, totalCount: nextProps.count, isinit: false }
        }
        return null
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.updatedData != prevProps.updatedData && this.props.updatedData && this.props.updatedData.page == 'aoio') {
            let { action, data } = this.props.updatedData;
            if (action == 'update') {
                let list = this.state.list.map((item) => {
                    if (item.id == data.id) {
                        return data;
                    }
                    return item;
                })
                this.setState({ list });
            } else if (action == 'add') {
                this.setState({ list: [data, ...this.state.list] })
            } else if (action == 'delete') {
                this.setState({ list: this.state.list.filter(item => { return item.id != data.id }) })
            }
            this.props.resetPageStore('updatedData')
        }
    }

    render() {
        let { list } = this.state;
        let { isLoader } = this.props;
        return (
            <div className='events indiaofideas'>
                <div className='row mb15'>
                    <div className='col-5'><div className='txt-title'>An India of Ideas</div></div>
                    <div className='col-2'>
                        <ModalButton
                            className="ico-Box"
                            data={{
                                showCustomModal: true,
                                title: '',
                                path: 'IndiaofIdeas/indiaofideas',
                                body: '',
                                className: 'modal-extra-large'
                            }}
                        >
                            <div className='btn w100per'>Add new <span className='ml15 fs16'><i className="fa fa-plus-circle"></i></span></div>
                        </ModalButton>
                    </div>
                    {/* <div className='col-2'><div className='btn w100per'>Edit About</div></div> */}
                </div>
                {isLoader && <Loader />}
                {!isLoader && (!list || list.length == 0) && <NoRecord />}
                <div className='list-scroll'>
                    <div className='row no-gutters'>
                        {!isLoader && list && list.length > 0 &&
                            list.map((item, i) => {
                                return (
                                    <div className='col-4' key={i}>
                                        <div className='eventList '>
                                            <div className='item-project'>
                                                <div href='#' className='item-click'>
                                                    <p className='mb7 eventTitle'>{item.title}</p>
                                                    <div className='image-style' style={{ backgroundImage: `url(${item.videoThumb})` }}>
                                                        <img src={'./images/imagesize16_9.png'} className='w100per' />
                                                    </div>
                                                </div>
                                            </div>
                                            <ModalButton
                                                className="ico-Box"
                                                data={{
                                                    showCustomModal: true,
                                                    id: item.id,
                                                    title: '',
                                                    path: 'IndiaofIdeas/indiaofideas',
                                                    body: '',
                                                    className: 'modal-extra-large'
                                                }}
                                            >
                                                <div className='d-flex justify-content-center mt5'><div className='btn w150'>Edit</div></div>
                                            </ModalButton>
                                            
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: { pageData: { data = [], count = 0 }, updatedData = {}, isLoader = false } }) {
    return { token, isLoader, list: data, count, updatedData }
}
export default connect(mapStateToProps, { getPageData, resetPageStore })(IndiaofIdeas);

