import React, { Component } from 'react';
import { connect } from 'react-redux';

import ModalButton from '../components/modal/button';
import { getPageData, resetPageStore, fetchSuggestions } from '../actions/page';
import { Endpoint } from '../utils/constants';
import Loader from '../components/customFields/loader';
import NoRecord from '../components/customFields/noRecord';
import { createComboList } from '../sagas/hooks'

class Events extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isinit: true,
            list: [],
            totalCount: '',
            VENUES: {},
            CATEGORY: {}
        };
    }

    componentDidMount() {
        this.props.fetchSuggestions({ url: `${Endpoint.EVENT_CAT}?token=${this.props.token}` });
        this.getList();
    }

    componentWillUnmount(){
        this.props.resetPageStore(['pageData', 'suggesions']);
    }

    getList = () => {
        this.props.getPageData({
            params: { token: this.props.token },
            url: Endpoint.EVENT_MANAGE
        })
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.list != prevState.list && prevState.isinit && nextProps.list.length) {
            return { list: nextProps.list, totalCount: nextProps.count, isinit: false }
        }
        return null
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.updatedData != prevProps.updatedData && this.props.updatedData && this.props.updatedData.page == 'event') {
            let { action, data } = this.props.updatedData;
            if (action == 'update') {
                let list = this.state.list.map((item) => {
                    if (item.id == data.id) {
                        return data;
                    }
                    return item;
                })
                this.setState({ list });
            } else if (action == 'add') {
                this.setState({ list: [data, ...this.state.list] })
            } else if (action == 'delete') {
                this.setState({ list: this.state.list.filter(item => { return item.id != data.id }) })
            }
            this.props.resetPageStore('updatedData')
        }
        if(this.props.suggesions != prevProps.suggesions && Object.keys(this.props.suggesions).length >0){
            this.setState({CATEGORY: this.props.suggesions.category && this.props.suggesions.category.length ?  createComboList(this.props.suggesions.category, 'id', 'category') : {}, 
            VENUES: this.props.suggesions.venue && this.props.suggesions.venue.length ?  createComboList(this.props.suggesions.venue, 'id', 'name') : {} })
       }
    }

    render() {
        let { list, VENUES, CATEGORY } = this.state;
        let { isLoader } = this.props;
        return (
            <div className='events'>
                <div className='row mb15'>
                    <div className='col-5'><div className='txt-title'>Events</div></div>
                    <div className='col-2'>
                        <ModalButton
                            className="ico-Box"
                            data={{
                                showCustomModal: true,
                                title: '',
                                CATEGORY,
                                VENUES,
                                path: 'event/addnewEvent',
                                body: '',
                                className: 'modal-large'
                            }}
                        >
                            <div className='btn w100per'>Add new event</div>
                        </ModalButton>

                    </div>
                </div>
                {isLoader && <Loader />}
                {!isLoader && (!list || list.length == 0) && <NoRecord />}
                <div className='event-scroll'>
                    <div className='row no-gutters'>

                        {!isLoader && list && list.length > 0 &&
                            list.map((item, i) => {
                                return (
                                    <div className='col-4' key={i}>
                                        <div className='eventList'>
                                            <div className='item-project'>
                                                <div href='#' className='item-click'>
                                                    <p className='mb7 eventTitle'>{item.title}</p>
                                                    <div className='image-style' style={{backgroundImage: `url(${item.image})`}}>
                                                        <img src={'./images/imagesize16_9.png'} className='w100per' />
                                                    </div>
                                                    <div className='row mt8'>
                                                        <div className='col-4'>{item.eventDate}</div>
                                                        <div className='col-8 text-capitalize text-right'>Nature of Event: {CATEGORY[item.nature] || item.nature}</div>
                                                    </div>
                                                    <div className='row mt5'>
                                                        <div className='col-3'>{item.eventTime}</div>
                                                        <div className='col-9 text-capitalize text-right'>Venue: {VENUES[item.venue] || item.venue}</div>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className='row no-gutters mt5'>
                                                <div className='col-4'>
                                                    <ModalButton
                                                        className="ico-Box"
                                                        data={{
                                                            showCustomModal: true,
                                                            id: item.id,
                                                            CATEGORY,
                                                            VENUES,
                                                            title: '',
                                                            path: 'event/addnewEvent',
                                                            body: '',
                                                            className: 'modal-large'
                                                        }}
                                                    >
                                                        <div className='btn w100per'>Edit</div>
                                                    </ModalButton>
                                                </div>
                                                <div className='col-8'>
                                                    <div className='pl10'>
                                                        <ModalButton
                                                            className="ico-Box"
                                                            data={{
                                                                showCustomModal: true,
                                                                title: '',
                                                                path: 'event/attendeesList',
                                                                body: '',
                                                                data: item,
                                                                id: item.id,
                                                                className: 'modal-large'
                                                            }}
                                                        >
                                                            <div className='btn w100per'>List of Attendees</div>
                                                        </ModalButton>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: { suggesions ={}, pageData: { data = [], count = 0 }, updatedData = {}, isLoader = false } }) {
    return { token, isLoader, list: data, count, updatedData, suggesions }
}
const mapDispatchToProps = function (dispatch, ownProps) {
    return {
        fetchSuggestions: myParam => dispatch(fetchSuggestions(myParam, 'suggesions')),
        getPageData: (myParam) => dispatch(getPageData(myParam)),
        resetPageStore: myParam => dispatch(resetPageStore(myParam))
    };
};
export default connect(mapStateToProps,mapDispatchToProps)(Events);
