import React, { Component } from "react";
import { connect } from "react-redux";

import { getPageData, updatePageData, resetPageStore, getIndividualItem } from "../actions/page";
import { Endpoint } from "../utils/constants";
import { dateToStr, exportToCsv, isToday } from "../utils/formatter";
import Loader from "../components/customFields/loader";
import NoRecord from "../components/customFields/noRecord";
import Chips from '../components/customFields/chips';
import InputField from '../components/customFields/inputField';
import ModalButton from '../components/modal/button';

import "../style/newStyle.scss";

class CheckedinMembers extends Component {
  constructor(props) {
    super(props);
    this.keymapper = {
      header: ["Check-in Time", "Member Name", "Member Id", 'Guests'],
      keys: ["checkinTime", "name", 'mno', "withGuest"]
    }
    this.state = {
      isinit: true,
      date: dateToStr(null, "YYYY-MM-DD"),
      list: [],
      totalCount: "",
      memberId: '',
      memberData: [],
      withGuest: '',
      isSaving: false,
      editId: ''
    };
  }

  componentDidMount() {
    this.getList();
  }

  componentWillUnmount() {
    this.props.resetPageStore("pageData");
  }

  getList = () => {
    this.props.getPageData({
      params: { token: this.props.token, date: this.state.date },
      url: Endpoint.CHECKIN_MEMBER
    });
  };

  static getDerivedStateFromProps(nextProps, prevState) {
    if (
      nextProps.list != prevState.list
    ) {
      return {
        list: nextProps.list,
        totalCount: nextProps.count,
        isinit: false
      };
    }
    return null;
  }

  inputChange = (e, id) => {
    let {
      target: { name, value }
    } = e;
    if (name === "withGuest" && value && value > 4) {
      this.setState({ error: "Maximum 4 guests are allowed." });
      return;
    }
    this.setState({ [name]: value, errorGuest: "" });
  };

  setChips = (value, name, item = {}) => {
    this.setState({
      [name]: value,
      memberId: value && value.length ? value[0].id : ""
    });
  };
  onSubmit = (e) => {
    e.preventDefault();
    if (this.state.memberId === '') {
      this.setState({ error: 'No Memebr Selected' });
      return;
    }
    if (!this.state.withGuest || parseInt(this.state.withGuest) <= 0) {
      this.setState({ error: 'Enter valid guest no.' });
      return;
    }
    this.setState({ isSaving: true });
    let { memberId, withGuest, editId } = this.state;
    let payload = { memberId, withGuest, token: this.props.token };
    this.props.updatePageData({
      payload,
      url: `${Endpoint.CHECKIN_MEMBER}${editId ? "/"+ editId : ''}`,
      method: editId ? "PUT" : "POST"
    }, (response) => {
      console.log(response);
      if (response.status) {
        this.setState({ error: '', isSaving: false, memberId: '', withGuest: '', memberData: [], editId: '' });
        this.getList();
        this.props.toastmessage(response.message);
      } else {
        this.setState({ error: response.message ? response.message : (response.errorMsg ? 'something went wrong.' : ''), errorGuest: response.message, isSaving: false })
      }
    });
  }

  loadData = (e) => {
    let { value, name } = e.target;
    this.editData(e, {});
    this.setState({ date: value }, () => {
      this.getList();
    });
  }

  editData = (e, item={})=>{
    this.setState({
      memberId: item.memberId || '',
      memberData: item.memberId && item.name ? [{name: item.name, id: item.memberId, mno: item.mno,image: item.image}] : [],
      withGuest: item.withGuest || '',
      editId: item.id || ''
    });
  }

  delete = (e, closeModal, item) => {
    e.preventDefault();
    let params = { token: this.props.token};
    this.props.updatePageData({
        params,
        url: `${Endpoint.CHECKIN_MEMBER}/${item.id}`,
        method: 'DELETE'
    }, (response) => {
        if (response.status) {
            closeModal();
            this.getList();
            this.setState({ error: '' });
        } else {
            this.setState({ error: response.message })
        }
    });
}

  render() {
    let { isLoader, token } = this.props;
    let { list, memberId, memberData, withGuest, error, isSaving, date, editId } = this.state;
    return (
      <div className="quorum_new">
        <div className="checkedin">
          <div className="row">
            <div className="col-md-4">
              <div className="checkedin_data">
               <div className='d-flex justify-content-between align-items-center'><p>Check-in A Member</p> {editId && <span className="backBtn" onClick={(e)=>{this.editData(e)}}>back</span>}</div>
               
                <Chips
                  className="checkIn-search"
                  placeholder="Search by First Name, Last Name or Member ID"
                  name="memberData"
                  namespace="suggesions"
                  disabled={editId ? true : false}
                  livesearch={true}
                  autocomplete={false}
                  isSingle={true}
                  service={`${Endpoint.MEMBER}?token=${token}`}
                  vk="id"
                  sk="name"
                  chips={memberData}
                  maxCount={1}
                  image="image"
                  stamp="mno"
                  isHighlight={true}
                  setChips={this.setChips}
                />
                <div className="d-flex">
                  <div className="w100per">
                    <p>Number Of Guests</p>
                    <InputField name="withGuest" groupClass="text-right" value={withGuest}
                      inputChange={this.inputChange} dom={{ placeholder: '0', type: 'number' }} />
                  </div>
                  <div className={`btn w100per d-flex align-items-center justify-content-center  ${memberId ? 'active' : ''} ${isSaving ? 'btndisabled' : ''}`} onClick={this.onSubmit}>
                    { editId ? "update" : "check-in"}
                  </div>
                </div>

                {error && <p className="error_msg mt5"> {error}</p>}
              </div>
              {memberId &&
                <div className="member_img">
                  <div className="image-style" style={{ backgroundImage: `url(${memberData[0].image})` }} >
                    <img src={"./images/imagesize2_3.png"} alt='' className="w100per" />
                  </div>
                </div>
              }
            </div>
            <div className="col-md">
              <div className='d-flex justify-content-end'>
                <div className='select-date'>
                  <InputField name="date" value={date} inputChange={this.loadData} dom={{ placeholder: 'Select Date', type: 'date', dateFormat: "dd/MM/yyyy", maxDate: new Date() }} />
                </div>
                {list && list.length > 0 && <div className='btn ml10' onClick={() => exportToCsv(list, 'checkin_list', this.keymapper)}>Download Report</div>}
              </div>
              {isLoader && <Loader />}
              {!isLoader && (!list || list.length == 0) && (
                <NoRecord message="No checked-in members found" />
              )}
              {!isLoader && list && list.length > 0 && (
                <div className="table_scroll">
                  <table>
                    <thead>
                      <tr className='text-center'>
                        <th className="w100">Time</th>
                        <th>Date</th>
                        <th>Member Name</th>
                        <th>Member ID</th>
                        <th>Guests</th>
                        {isToday(date) && <th>Actions</th>}
                      </tr>
                    </thead>
                    <tbody>
                      {list.map((item, i) => {
                        return (
                          <tr key={i} className='text-center'>
                            <td className="w100">{dateToStr(item.checkinTime, 'HH:mm')}</td>
                            <td>{dateToStr(item.checkinTime, 'DD/MM/YYYY')}</td>
                            <td>{item.name}</td>
                            <td>{item.mno}</td>
                            <td>{item.withGuest}</td>
                            {isToday(date) && <td>
                              <span className='ml20 cursor-pointer color-white' onClick={(e)=>{this.editData(e, item)}}><i className='fa fa-edit'></i></span>
                              <ModalButton
                                  className="ml20 cursor-pointer color-red fs18"
                                  tag="span"
                                  data={{
                                      showCustomModal: true,
                                      id: item.id,
                                      title: '',
                                      footer: true,
                                      onSubmit: this.delete,
                                      submitArg: {id: item.id},
                                      body: 'Are you sure want to delete it',
                                      className: ''
                                  }}
                              >
                                  <i className='fa fa-remove'></i>
                              </ModalButton>
                            </td>
                            }
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
function mapStateToProps({
  auth: { token },
  page: {
    pageData: { data = [], count = 0 },
    updatedData = {},
    isLoader = false
  }
}) {
  return { token, isLoader, list: data, count, updatedData };
}
export default connect(
  mapStateToProps,
  { getPageData, updatePageData, resetPageStore, getIndividualItem }
)(CheckedinMembers);
