import React, { Component } from 'react';
import { connect } from 'react-redux';

import { getPageData, resetPageStore } from '../../actions/page';
import { Endpoint } from '../../utils/constants';
import Loader from '../customFields/loader';
import InputField from '../customFields/inputField';
import NoRecord from '../customFields/noRecord';
import Chips from '../customFields/chips';


class Memberlist extends Component {
    constructor(props) {
        super(props);
        this.state = {
            keyword: '',
            memberId: '',
            memberId_name: [],
            isinit: true,
            isReset: false,
            list: [],
            totalCount: ''
        };
    }
    componentDidMount() {
        let { match: { params } } = this.props;
        if (!params.mtype || ['member', 'staf-member'].indexOf(params.mtype) == -1) {
            this.props.history.push('/dashboard');
        } else {
            this.getList(params.mtype);
        }
    }

    getList = (type) => {
        let { keyword, memberId } = this.state;
        this.props.getPageData({
            params: { token: this.props.token, keyword: memberId ? '' : keyword, rid: memberId },
            url: type == 'member' ? Endpoint.MEMBER : Endpoint.STAF_MEMBER
        })
    }

    reset = (type) =>{
        this.setState({keyword: '', memberId: '', memberId_name: [], isReset: true},()=>{
            this.getList(type);
        });
    }

    componentWillUnmount() {
        this.props.resetPageStore('pageData');
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.list != prevState.list) {
            return { list: nextProps.list, totalCount: nextProps.count, isinit: false, isReset:false }
        }
        return null
    }

    componentDidUpdate(prevProps, prevState) {
        let { match: { params } } = this.props;
        if (params.mtype !== prevProps.match.params.mtype) {
            if (['member', 'staf-member'].indexOf(params.mtype) == -1) {
                this.props.history.push('/dashboard');
            } else {
                this.getList(params.mtype);
            }
        }
    }

    inputChange = (e) => {
        let { target: { name, value } } = e;
        this.setState({ [name]: value, isinit: true })
    }

    goto = (e, item) =>{
        e.preventDefault();
        let { match: { params } } = this.props;
        if(params.mtype === 'member'){
            this.props.history.push(`/edit-member/${item.id}`);
        }else if(params.mtype === 'staf-member'){
            this.props.history.push(`/edit-staffmember/${item.id}`);
        }
    }

    setChips = (value, name, item = {}) => {
        this.setState({ [name]: item.id, [name + '_name']: value });
    };

    render() {
        let { list, keyword, memberId_name, memberId, isReset } = this.state;
        let { token, isLoader, match: { params } } = this.props;
        return (
            <div className='member'>
                <div className='row justify-content-between'>
                    <div className='col-5'>
                        <div className='_title'>{params.mtype == 'member' ? 'member list' : 'staff member list'}</div>
                    </div>
                    <div className='col-4'>
                        <div className="d-flex mb20">
                            <div className="w100per search-input left-rounded-input">
                            <Chips
                                placeholder={params.mtype == 'member' ? 'name, member no.' : 'name'}
                                className=""
                                name="memberId" 
                                namespace={params.mtype == 'member' ? 'suggesions' : "countryList"}
                                livesearch={true}
                                autocomplete={false}
                                service={`${Endpoint.AUTOSUGGETIONS}?token=${token}&type=${params.mtype == 'member' ? 'member': 'staf-member'} `}
                                vk="id"
                                sk="name"
                                image={params.mtype == 'member' ? "image" : ''}
                                chips={memberId_name}
                                maxCount={1}
                                isReset={isReset}
                                singlechipstyle={{margin: 0}}
                                onInputChage={(v)=>{this.inputChange({target:{name: 'keyword', value: v}})}}
                                setChips={this.setChips}
                            />
                                {/* <InputField name="keyword" value={keyword} inputChange={this.inputChange}
                                    dom={{ placeholder: `Search by ${params.mtype == 'member' ? 'name, member no.' : 'name'}` }} /> */}
                            </div>
                            <span className="btn btn-sm right-rounded-btn search-btn" onClick={()=>{this.getList(params.mtype)}}>Search</span>
                            <span className="btn btn-sm ml10 search-btn w150" onClick={()=>{this.reset(params.mtype)}}>reset</span>
                        </div>
                    </div>
                </div>
                {isLoader && <Loader />}
                {!isLoader && (!list || list.length == 0) && <NoRecord />}
                <div className='list-scroll'>
                    <div className='d-flex flex-wrap'>
                        {!isLoader && list && list.length > 0 && list.map((item, i) => {
                            return (
                                <a className='list-item cursor-pointer' key={item.id}  onClick={(e)=>this.goto(e, item)}>
                                    <div className='flex-column'>
                                        <div className='image-style' style={{ backgroundImage: `url(${item.image})` }}>
                                            <img src='../../images/imagesize2_3.png' className='w100per' />
                                        </div>
                                        <p className='color-white lh18 mt8'>{`${item.firstName}${item.middleName ? " " + item.middleName + " " : " "}${item.lastName}`}</p>
                                        {params.mtype == 'member' && <p className='fs14 color-green'>M. No.- {item.mno}</p>}
                                    </div>
                                </a>)
                        })}
                    </div>
                </div>
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: { pageData: { data = [], count = 0 }, updatedData = {}, isLoader = false } }) {
    return { token, isLoader, list: data, count, updatedData }
}
export default connect(mapStateToProps, { getPageData, resetPageStore })(Memberlist);
