import React, { Component } from 'react';
import { connect } from 'react-redux';
import Dropzone from 'react-dropzone';

import InputField from '../customFields/inputField'
import { getValue } from '../../utils/formatter';
import { isObject, isObjEmpty } from '../../utils';
import { validate } from '../../utils/validation';
import { ALTERNATE_CONTACTTYPE, MEMBER_TITLE, MERITAL_STATUS } from '../../utils/constants';
import { updatePageData, getIndividualItem, resetPageStore, fetchSuggestions } from '../../actions/page';
import { createComboList } from '../../sagas/hooks'

import { Endpoint } from '../../utils/constants';

class AddMember extends Component {
    constructor(props) {
        super(props);
        this.rf  = ["phone", "dob", "firstName", "lastName", "mno", "mType", "address", "afName",  "aphone", "mfName",  "mphone"];
        this.state = this.intialiseState()
    }

    intialiseState = (data = {}, isinit = true) => {
        let mt = getValue(data, 'mtype', 'm1');
        return {
            image: getValue(data, 'image', ''),
            phone: getValue(data, 'phone', ''),
            email: getValue(data, 'email', ''),
            dob: getValue(data, 'dob', null),
            title: getValue(data, 'title', 'm'),
            otitle: getValue(data, 'otitle', ''),
            firstName: getValue(data, 'firstName', ''),
            middleName: getValue(data, 'middleName', ''),
            lastName: getValue(data, 'lastName', ''),
            address: getValue(data, 'address', ''),
            company: getValue(data, 'company', ''),
            industry: getValue(data, 'industry', ''),
            jobTitle: getValue(data, 'jobTitle', ''),
            mno: getValue(data, 'mno', ''),
            mType: getValue(data, 'mType', ''),
            startDate: getValue(data, 'startDate', null),
            endDate: getValue(data, 'endDate', null),
            atype: getValue(data, 'atype', 1),
            afName: getValue(data, 'afName', ''),
            alName: getValue(data, 'alName', ''),
            aemail: getValue(data, 'aemail', ''),
            aphone: getValue(data, 'aphone', ''),
            mtype: mt === 'm' ? 'm1' : mt,
            mfName: getValue(data, 'mfName', ''),
            mlName: getValue(data, 'mlName', ''),
            memail: getValue(data, 'memail', ''),
            mphone: getValue(data, 'mphone', ''),
            mdob: getValue(data, 'mdob', null),
            manniversary: getValue(data, 'manniversary', null),
            founderName1: getValue(data, 'founderName1', ''),
            founderName2: getValue(data, 'founderName2', ''),
            founderNote: getValue(data, 'founderNote', ''),
            fDrink: getValue(data, 'fDrink', ''),
            fFilm: getValue(data, 'fFilm', ''),
            fitnessActivity: getValue(data, 'fitnessActivity', ''),
            fherb: getValue(data, 'fherb', ''),
            hobby: getValue(data, 'hobby', {}),
            status: getValue(data, 'status', 0),
            errorMessages: {},
            isValid: {},
            isinit,
            isSaving: false
        }
    }

    componentDidMount() {
        let { match: { params } } = this.props;
        this.props.fetchSuggestions({ url: `${Endpoint.MEMBER_CATEGORIES}?token=${this.props.token}&mtype=1` });
        if (params.mid) {
            this.props.getIndividualItem({
                params: { token: this.props.token },
                url: `${Endpoint.MEMBER}/${params.mid}`
            });
        }
    }

    componentWillUnmount() {
        this.props.resetPageStore(['individualItem', 'suggesions']);
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.itemData != this.props.itemData && prevState.isinit && isObject(this.props.itemData) && !isObjEmpty(this.props.itemData)) {
            let stateD = this.intialiseState(this.props.itemData, false);
            this.setState(stateD);
        }
        if(this.props.suggesions != prevProps.suggesions && Object.keys(this.props.suggesions).length >0){
             this.setState({MEMBERTYPE: this.props.suggesions.membershipType && this.props.suggesions.membershipType.length ?  createComboList(this.props.suggesions.membershipType, 'id', 'memberType') : {}, HOBBIES: this.props.suggesions.hobbies || []})
        }
    }

    validate = (name, value) => {
        if(this.rf.indexOf(name) > -1 || value){
            let { isValid, errorMessages } = this.state;
            let error = validate(name, value, { type: 'text', regexType: ['email', 'aemail', 'memail'].indexOf(name) > -1 ? 'email' :'noop' });
            isValid[name] = error.isValid;
            error.isValid
                ? delete errorMessages[name]
                : (errorMessages[name] = error.errorMsg);
            this.setState({ isValid, errorMessages });
        }else{
            let { isValid, errorMessages } = this.state;
            isValid[name] = true;
            delete errorMessages[name];
            this.setState({ isValid, errorMessages });
        }
    };

    inputChange = e => {
        const {
            target: { name, value }
        } = e;
        let so = { [name]: value }
        if (name === 'otitle' && value) {
            so.title = '';
        } else if (name === 'title' && value) {
            so.otitle = '';
        }
        this.setState(so, () => {
           // name = name === 'otitle' ? 'title' : name;
            this.validate(name, value);
        });
    };

    selectHobby = (e) => {
        e.preventDefault();
        let value = e.target.getAttribute('value');
        let hobby = this.state.hobby;
        hobby[value] = hobby[value] == 1 ? 0 : 1
        this.setState({ hobby })
    }

    onSubmit = (e) => {
        e.preventDefault();
        let { match: { params } } = this.props;
        let rf = [...this.rf, 'email', 'aemail', 'memail'];
        rf.forEach((name) => {
            let value = this.state[name];
            if ((name == "afName" || name == "aphone") && this.state.atype == 3) {
                value = 1
            } else if ((name == "mfName" || name == "mphone") && this.state.mtype == 's') {
                value = 1
            }
            this.validate(name, value);
        });
        if (Object.keys(this.state.errorMessages).length == 0) {
            this.setState({ error: '', isSaving: true });
            let payload = Object.assign({}, this.state);
            payload.token = this.props.token
            payload.title = payload.otitle ? payload.otitle : payload.title;
            payload.mtype = payload.mtype === 'm1' ? 'm' : payload.mtype;
            delete payload.errorMessages;
            delete payload.isValid;
            this.props.updatePageData({
                payload,
                url: `${Endpoint.MEMBER}${params.mid ? '/' + params.mid : ''}`,
                method: params.mid ? "PUT" : 'POST'
            }, (response) => {
                if (response.status) {
                    this.setState({ error: '', isSaving: false });
                    this.props.toastmessage(response.message);
                    this.props.history.push('/member-list/member');
                } else {
                    this.setState({ error: response.message, isSaving: false })
                }
            });
        }
    }

    deleteItem = (e) => {
        let { match: { params } } = this.props;
        this.props.updatePageData({
            payload: {},
            params: { token: this.props.token, status: this.state.status === 1 ? 0 : 1 },
            url: `${Endpoint.MEMBER}/${params.mid}`,
            method: 'DELETE',
        }, (response) => {
            if (response.status) {
                this.setState({ error: '' });
                this.props.toastmessage(response.message);
                this.props.history.push('/member-list/member');
            } else {
                this.setState({ error: response.message })
            }
        });
    }

    onDrop = (a, r) => {
        if (r.length || !a.length) {
            return
        }
        let reader = new FileReader();
        reader.onloadend = () => {
            this.setState({ image: reader.result, isImageAdded: true });
        }
        reader.readAsDataURL(a[0]);
    }


    render() {
        let {MEMBERTYPE={}, HOBBIES = [], error, image, errorMessages, isValid, phone, email, dob, title, otitle, firstName, middleName, lastName, address, company, industry, jobTitle, mno, mType, startDate, endDate, atype, afName, alName, aemail, aphone, mtype, mfName, mlName, memail, mphone, mdob, manniversary, founderName1, founderName2, founderNote, fDrink, fFilm, fitnessActivity, fherb, hobby, isSaving, status } = this.state;
        let { match: { params } } = this.props;
        return (
            <div className='member-form'>
                <div className='_title mb0'>{params.mid ? 'EDIT MEMBER' : 'CREATE A NEW MEMBER'}</div>
                <div className='row'>
                    <div className='col-4'>
                        <div className='left-content'>
                            <div className='row mb5'>
                                <div className='col-4'>
                                    <Dropzone
                                        style={{ position: 'relative' }}
                                        accept="image/jpeg, image/png"
                                        multiple={false}
                                        onDrop={(accepted, rejected) => { this.onDrop(accepted, rejected) }}>
                                        <div className={'image-style upload-photo ' + (image && 'uploadtxt')} style={{ backgroundImage: `url(${image})` }}>
                                            <img src={'../../images/imagesize2_3.png'} className='w100per' />
                                        </div>
                                    </Dropzone>
                                </div>
                                <div className='col-8'>
                                    <div><InputField label = "Phone" name="phone" value={phone} isValid={isValid.phone} errorMsg={errorMessages.phone}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Phone No. *', type: 'number' }} /></div>
                                    <div><InputField name="email" value={email} isValid={isValid.email} errorMsg={errorMessages.email}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Email', type: 'email' }} /></div>
                                    <div><InputField name="dob" value={dob} isValid={isValid.dob} errorMsg={errorMessages.dob}
                                        inputChange={this.inputChange} dom={{ placeholder: 'DOB *', type: 'date' }} /></div>
                                </div>
                            </div>
                            <div className='row'>
                                <div className='col-8'>
                                    <div className='d-flex justify-content-between align-items-center h100per'>
                                        <InputField name="title" value={title} isValid={isValid.title} errorMsg={errorMessages.title}
                                            inputChange={this.inputChange} options={MEMBER_TITLE} dom={{ type: 'radio' }} />
                                    </div>
                                </div>
                                <div className='col-4'>
                                    <div><InputField name="otitle" value={otitle} isValid={isValid.otitle} errorMsg={errorMessages.otitle}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Type other' }} /></div>
                                </div>
                            </div>
                            <div><InputField name="firstName" value={firstName} isValid={isValid.firstName} errorMsg={errorMessages.firstName}
                                inputChange={this.inputChange} dom={{ placeholder: 'First Name *' }} /></div>
                            <div><InputField name="middleName" value={middleName} isValid={isValid.middleName} errorMsg={errorMessages.middleName}
                                inputChange={this.inputChange} dom={{ placeholder: 'Middle Name' }} /></div>
                            <div><InputField name="lastName" value={lastName} isValid={isValid.lastName} errorMsg={errorMessages.lastName}
                                inputChange={this.inputChange} dom={{ placeholder: 'Last Name *' }} /></div>
                            <div><InputField name="address" value={address} isValid={isValid.address} errorMsg={errorMessages.address}
                                inputChange={this.inputChange} dom={{ placeholder: 'Address and Pin Code *' }} /></div>
                            <div><InputField name="company" value={company} isValid={isValid.company} errorMsg={errorMessages.company}
                                inputChange={this.inputChange} dom={{ placeholder: 'Company' }} /></div>
                            <div className='row'>
                                <div className='col-6'>
                                    <div><InputField name="industry" value={industry} isValid={isValid.industry} errorMsg={errorMessages.industry}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Industry' }} /></div>
                                </div>
                                <div className='col-6'>
                                    <div><InputField name="jobTitle" value={jobTitle} isValid={isValid.jobTitle} errorMsg={errorMessages.jobTitle}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Job Title' }} /></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='col-4'>
                        <div className='middle-content'>
                            <div className='row'>
                                <div className='col-6'>
                                    <div><InputField name="mno" value={mno} isValid={isValid.mno} errorMsg={errorMessages.mno}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Membership No. *' }} /></div>
                                    <div><InputField name="startDate" value={startDate} isValid={isValid.startDate} errorMsg={errorMessages.startDate}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Start Date', type: 'date' }} /></div>
                                </div>
                                <div className='col-6'>
                                    <div className='mem_type'><InputField name="mType" value={mType} isValid={isValid.mType} errorMsg={errorMessages.mType}
                                        inputChange={this.inputChange} options={MEMBERTYPE} dom={{ placeholder: 'Member Type', type: 'select' }} /></div>
                                    <div><InputField name="endDate" value={endDate} isValid={isValid.endDate} errorMsg={errorMessages.endDate}
                                        inputChange={this.inputChange} dom={{ placeholder: 'End Date', type: 'date' }} /></div>
                                </div>
                            </div>

                            <div className='alter-contact'>
                                <label className='_label'>Alternate Contact</label>
                                <div className='d-flex justify-content-between'>
                                    <InputField name="atype" value={atype} isValid={isValid.atype} errorMsg={errorMessages.atype}
                                        inputChange={this.inputChange} options={ALTERNATE_CONTACTTYPE} dom={{ type: 'radio' }} />
                                </div>
                            </div>
                            {atype != 3 &&
                            <div className='row'>
                                <div className='col-6'>
                                    <div><InputField name="afName" value={afName} isValid={isValid.afName} errorMsg={errorMessages.afName}
                                        inputChange={this.inputChange} dom={{ placeholder: 'First Name *' }} /></div>
                                    <div><InputField name="aemail" value={aemail} isValid={isValid.aemail} errorMsg={errorMessages.aemail}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Email' }} /></div>
                                </div>
                                <div className='col-6'>
                                    <div><InputField name="alName" value={alName} isValid={isValid.alName} errorMsg={errorMessages.alName}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Last Name' }} /></div>
                                    <div><InputField name="aphone" value={aphone} isValid={isValid.aphone} errorMsg={errorMessages.aphone}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Phone No. *' }} /></div>
                                </div>
                            </div>
                            }
                            <div className='mb8'>
                                <label className='_label'>Marital Status</label>
                                <div className='d-flex justify-content-between'>
                                    <InputField name="mtype" value={mtype} isValid={isValid.mtype} errorMsg={errorMessages.mtype}
                                        inputChange={this.inputChange} options={MERITAL_STATUS} dom={{ type: 'radio' }} />
                                </div>
                            </div>
                            {mtype != 's' &&
                                <div className='row'>
                                    <div className='col-6'>
                                        <div><InputField name="mfName" value={mfName} isValid={isValid.mfName} errorMsg={errorMessages.mfName}
                                            inputChange={this.inputChange} dom={{ placeholder: 'Spouse First Name *' }} /></div>
                                        <div><InputField name="memail" value={memail} isValid={isValid.memail} errorMsg={errorMessages.memail}
                                            inputChange={this.inputChange} dom={{ placeholder: 'Email *', type: 'email' }} /></div>
                                        <div><InputField name="mdob" value={mdob} isValid={isValid.mdob} errorMsg={errorMessages.mdob}
                                            inputChange={this.inputChange} dom={{ placeholder: 'DOB', type: 'date' }} /></div>
                                    </div>
                                    <div className='col-6'>
                                        <div><InputField name="mlName" value={mlName} isValid={isValid.mlName} errorMsg={errorMessages.mlName}
                                            inputChange={this.inputChange} dom={{ placeholder: 'Spouse Last Name' }} /></div>
                                        <div><InputField name="mphone" value={mphone} isValid={isValid.mphone} errorMsg={errorMessages.mphone}
                                            inputChange={this.inputChange} dom={{ placeholder: 'Phone No. *', type: 'number' }} /></div>
                                        <div><InputField name="manniversary" value={manniversary} isValid={isValid.manniversary} errorMsg={errorMessages.manniversary}
                                            inputChange={this.inputChange} dom={{ placeholder: 'Anniversary', type: 'date' }} /></div>
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                    <div className='col-4'>
                        <div className='right-content'>
                            <label className='_label color-white mb9'>Founder Member Reference</label>
                            <div className='row'>
                                <div className='col-6'>
                                    <div><InputField name="founderName1" value={founderName1} isValid={isValid.founderName1} errorMsg={errorMessages.founderName1}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Name 1' }} /></div>
                                </div>
                                <div className='col-6'>
                                    <div><InputField name="founderName2" value={founderName2} isValid={isValid.founderName2} errorMsg={errorMessages.founderName2}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Name 2' }} /></div>
                                </div>
                            </div>
                            <div><textarea className='form_control noteMember' placeholder='Notes on Member' name='founderNote' value={founderNote} onChange={this.inputChange}></textarea></div>
                            <div className='row'>
                                <div className='col-6'>
                                    <div><InputField name="fDrink" value={fDrink} isValid={isValid.fDrink} errorMsg={errorMessages.fDrink}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Favourite Drink' }} /></div>
                                </div>
                                <div className='col-6'>
                                    <div><InputField name="fFilm" value={fFilm} isValid={isValid.fFilm} errorMsg={errorMessages.fFilm}
                                        inputChange={this.inputChange} dom={{ placeholder: 'Favourite Film' }} /></div>
                                </div>
                            </div>
                            <div><InputField name="fitnessActivity" value={fitnessActivity} isValid={isValid.fitnessActivity} errorMsg={errorMessages.fitnessActivity}
                                inputChange={this.inputChange} dom={{ placeholder: 'Go to Fitness Activity' }} /></div>
                            <div><InputField name="fherb" value={fherb} isValid={isValid.fherb} errorMsg={errorMessages.fherb}
                                inputChange={this.inputChange} dom={{ placeholder: 'Favourite Herb/Spice' }} /></div>

                            <label className='_label m0'>Select</label>
                            <div className='_tags d-flex justify-content-between flex-wrap'>
                                {HOBBIES.map((h, i) => {
                                    return <div className={`${hobby[h.slug] == 1 ? 'active' : ''} select-tag`} key={i} value={h.slug} onClick={this.selectHobby}>{h.title}</div>
                                })
                                }
                            </div>
                        </div>
                    </div>
                </div>
                {error && <p className="error_msg"> {error}</p>}
                <div className='text-right mt20'>
                {params.mid && <div className='btn mr10' onClick={this.deleteItem}>{status === 1 ? 'Inactive' : 'Active'}</div>}
                <div className={`btn w150 ${isSaving ? 'btndisabled' : ''}`} onClick={this.onSubmit}>{params.mid ? 'Update': 'Save'}</div>
                </div>
            </div>
        );
    }
}

function mapStateToProps({ auth: { token }, page: { suggesions = {}, individualItem: { data = {} }, itemLoader = false } }) {
    return { token, itemData: data, itemLoader, suggesions }
}

const mapDispatchToProps = function (dispatch, ownProps) {
    return {
        fetchSuggestions: myParam => dispatch(fetchSuggestions(myParam, 'suggesions')),
        updatePageData: (myParam, cb) => dispatch(updatePageData(myParam, cb)),
        getIndividualItem: (myParam) => dispatch(getIndividualItem(myParam)),
        resetPageStore: myParam => dispatch(resetPageStore(myParam))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(AddMember);
