import React, { Component } from 'react';
import { connect } from 'react-redux';
import Dropzone from 'react-dropzone';
import { Dropdown, DropdownMenu, DropdownToggle } from 'reactstrap';

import InputField from '../customFields/inputField'
import { getValue } from '../../utils/formatter';
import { validate } from '../../utils/validation';
import { isObject, isObjEmpty } from '../../utils';
import { GENDER } from '../../utils/constants';
import { updatePageData, getIndividualItem, resetPageStore, fetchSuggestions } from '../../actions/page';
import { Endpoint } from '../../utils/constants';
import { createComboList, createMuliselectList } from '../../sagas/hooks';


class Staffmember extends Component {
  constructor(props) {
    super(props);
    this.state = this.intialiseState();
  }

  intialiseState = (data = {}, isinit = true) => {
    return {
      image: getValue(data, 'image', ''),
      firstName: getValue(data, 'firstName', ''),
      middleName: getValue(data, 'middleName', ''),
      lastName: getValue(data, 'lastName', ''),
      phone: getValue(data, 'phone', ''),
      gender: getValue(data, 'gender', 'm'),
      email: getValue(data, 'email', ''),
      dob: getValue(data, 'dob', null),
      address: getValue(data, 'address', ''),
      roleTitle: getValue(data, 'roleTitle', ''),
      clearanceLevel: getValue(data, 'clearanceLevel', ''),
      joinDate: getValue(data, 'joinDate', null),
      notes: getValue(data, 'notes', ''),
      cid: data.cid ? data.cid.split(',') : [],
      errorMessages: {},
      isValid: {},
      isinit,
      isSaving: false,
      isOpen: false
    }
  }

  componentDidMount() {
    this.props.fetchSuggestions({ url: `${Endpoint.CLUB_MANAGE}?token=${this.props.token}&type=all` });
    let { match: { params } } = this.props;
    if (params.mid) {
      this.props.getIndividualItem({
        params: { token: this.props.token },
        url: `${Endpoint.STAF_MEMBER}/${params.mid}`
      });
    }
  }

  componentWillUnmount() {
    this.props.resetPageStore('individualItem');
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.itemData != this.props.itemData && prevState.isinit && isObject(this.props.itemData) && !isObjEmpty(this.props.itemData)) {
      let stateD = this.intialiseState(this.props.itemData, false);
      this.setState(stateD);
    }
    if (this.props.suggesions != prevProps.suggesions && Object.keys(this.props.suggesions).length > 0) {
      this.setState({
        CLUBS: this.props.suggesions.club && this.props.suggesions.club.length ? createComboList(this.props.suggesions.club, 'id', 'clubName') : {},
        CLEARANCE_LEVEL: this.props.suggesions.accessLevel && this.props.suggesions.accessLevel.length ? createComboList(this.props.suggesions.accessLevel, 'id', 'name') : {}
      })
    }
  }

  validate = (name, value) => {
    let notr = ['middleName', 'image'];
    if (notr.indexOf(name) > -1) {
      return;
    }
    let { isValid, errorMessages } = this.state;
    let error = validate(name, value, { type: 'text' });
    isValid[name] = error.isValid;
    error.isValid
      ? delete errorMessages[name]
      : (errorMessages[name] = error.errorMsg);
    this.setState({ isValid, errorMessages });
  };

  inputChange = e => {
    let {
      target: { name, value }
    } = e;
    this.setState({ [name]: value }, () => {
      this.validate(name, value);
    });
  };

  toggle = () => {
    this.setState({ isOpen: !this.state.isOpen });
  }

  handleDropD = (e) => {
    let {
      target: { name, value, checked }
    } = e;
    let { cid } = this.state;
    value = value ? parseInt(value) : '';
    let i = cid.indexOf(value);
    if (i > -1) {
      cid.splice(i);
    } else {
      cid = [...cid, value];
    }
    this.setState({ cid, isOpen: false });
  }

  handleChange = (e) => {
    var options = e.target.options;
    var value = [];
    for (var i = 0, l = options.length; i < l; i++) {
      if (options[i].selected && options[i].value !== '') {
        value.push(options[i].value);
      }
    }
    this.setState({ cid: value });
  }

  getClubNames = (ids, clubs) => {
    let name = ids.map((id) => {
      return clubs[id];
    })
    return name.join(', ');
  }

  onSubmit = (e) => {
    e.preventDefault();
    ["firstName", "lastName", "phone", "email", "dob", "roleTitle", "clearanceLevel", "joinDate", "gender", 'cid'].forEach((name) => {
      let value = name === 'cid' ? (this.state[name].length > 0 ? 'v' : '') : this.state[name];
      this.validate(name, value);
    });
    if (Object.keys(this.state.errorMessages).length == 0) {
      this.setState({ error: '', isSaving: true });
      let payload = Object.assign({}, this.state);
      let { match: { params } } = this.props;
      payload.token = this.props.token
      delete payload.errorMessages;
      delete payload.isValid;
      payload.cid = payload.cid.join(',');
      this.props.updatePageData({
        payload,
        url: `${Endpoint.STAF_MEMBER}${params.mid ? '/' + params.mid : ''}`,
        method: params.mid ? "PUT" : 'POST'
      }, (response) => {
        if (response.status) {
          this.setState({ error: '', isSaving: false });
          this.props.toastmessage(response.message);
          this.props.history.push('/member-list/staf-member');
        } else {
          this.setState({ error: response.message, isSaving: false })
        }
      });
    }
  }

  onDrop = (a, r) => {
    if (r.length || !a.length) {
      return
    }
    let reader = new FileReader();
    reader.onloadend = () => {
      this.setState({ image: reader.result, isImageAdded: true });
    }
    reader.readAsDataURL(a[0]);
  }


  render() {
    let { cid, isOpen, isSaving, image, firstName, middleName, lastName, phone, email, dob, address, roleTitle, clearanceLevel, joinDate, gender, notes, errorMessages, isValid, CLEARANCE_LEVEL = {}, CLUBS = {} } = this.state;
    let { match: { params } } = this.props;
    return (
      <div className='member-form staff-member'>
        <div className='_title mb0'>{params.mid ? 'EDIT STAFF MEMBER' : 'CREATE A STAFF MEMBER'}</div>
        <div className='row'>
          <div className='col-6'>
            <div className='left-content'>
              <div className='row no-gutters mb10'>
                <div className='col-4'>
                  <Dropzone
                    style={{ position: 'relative', paddingRight: '50px' }}
                    accept="image/jpeg, image/png"
                    multiple={false}
                    onDrop={(accepted, rejected) => { this.onDrop(accepted, rejected) }}>
                    <div className={'image-style upload-photo ' + (image && 'uploadtxt')} style={{ backgroundImage: `url(${image})` }}>
                      <img src={'../../images/imagesize2_3.png'} className='w100per' />
                    </div>
                  </Dropzone>
                </div>
                <div className='col-8'>
                  <div className='d-flex mb15'>
                    <InputField name="gender" groupClass="mr30" value={gender} isValid={isValid.gender} errorMsg={errorMessages.gender}
                      inputChange={this.inputChange} options={GENDER} dom={{ type: 'radio' }} />
                  </div>
                  <div><InputField name="firstName" value={firstName} isValid={isValid.firstName} errorMsg={errorMessages.firstName}
                    inputChange={this.inputChange} dom={{ placeholder: 'First Name *' }} /></div>
                  <div><InputField name="middleName" value={middleName} isValid={isValid.middleName} errorMsg={errorMessages.middleName}
                    inputChange={this.inputChange} dom={{ placeholder: 'Middle Name' }} /></div>
                  <div><InputField name="lastName" value={lastName} isValid={isValid.lastName} errorMsg={errorMessages.lastName}
                    inputChange={this.inputChange} dom={{ placeholder: 'Last Name *' }} /></div>
                  <div><InputField name="dob" value={dob} isValid={isValid.dob} errorMsg={errorMessages.dob}
                    inputChange={this.inputChange} dom={{ placeholder: 'DOB *', type: 'date' }} /></div>
                </div>
              </div>
              <div className='row'>
                <div className='col-6'>
                  <div><InputField name="email" value={email} isValid={isValid.email} errorMsg={errorMessages.email}
                    inputChange={this.inputChange} dom={{ placeholder: 'Email *', type: 'email' }} /></div>
                </div>
                <div className='col-6'>
                  <div><InputField name="phone" value={phone} isValid={isValid.phone} errorMsg={errorMessages.phone}
                    inputChange={this.inputChange} dom={{ placeholder: 'Phone No. *', type: 'number' }} /></div>
                </div>
              </div>

              <div><textarea className='form_control noteMember' name="address" value={address} onChange={this.inputChange} placeholder='Address'></textarea></div>
            </div>
          </div>
          <div className='col-6'>
            <div className='right-content'>
              <div className='row'>
                <div className='col-6'>
                  <div><InputField name="roleTitle" value={roleTitle} isValid={isValid.roleTitle} errorMsg={errorMessages.roleTitle}
                    inputChange={this.inputChange} dom={{ placeholder: 'Role *' }} /></div>
                  <div><InputField name="joinDate" value={joinDate} isValid={isValid.joinDate} errorMsg={errorMessages.joinDate}
                    inputChange={this.inputChange} dom={{ placeholder: 'Join Date *', type: 'date' }} /></div>
                </div>
                <div className='col-6'>
                  <div><InputField name="clearanceLevel" value={clearanceLevel} isValid={isValid.clearanceLevel} errorMsg={errorMessages.clearanceLevel}
                    inputChange={this.inputChange} options={CLEARANCE_LEVEL} dom={{ placeholder: 'Clearance Level', type: 'select' }} /></div>
                  <div>
                    <Dropdown isOpen={isOpen} toggle={this.toggle} className="form_control select-club">
                      <DropdownToggle caret
                        tag="div"
                        data-toggle="dropdown"
                        aria-expanded={isOpen}
                      >
                        {cid && cid.length ? this.getClubNames(cid, CLUBS) : "Select Clubs"}
                      </DropdownToggle>
                      <DropdownMenu>
                        {Object.keys(CLUBS).length > 0 && Object.keys(CLUBS).map((item, i) => {
                          return (
                            <div className="d-flex mb5" key={i}>
                              <input type="checkbox" checked={cid.indexOf(parseInt(item)) > -1}
                                value={item} name={item} onChange={this.handleDropD} />
                              <p>{CLUBS[item]}</p>
                            </div>
                          );
                        })}
                      </DropdownMenu>
                    </Dropdown>
                    {/* <InputField name="cid" value={cid} isValid={isValid.cid} errorMsg={errorMessages.cid}
                    inputChange={this.handleChange} options={CLUBS} dom={{ placeholder: 'Clubs', type: 'select', multiple:true }} /> */}
                  </div>
                </div>
              </div>
              <textarea className='form_control noteMember' name="notes" value={notes} onChange={this.inputChange} placeholder='Notes on Employee'></textarea>
            </div>
          </div>
        </div>
        <div className='text-right'>
          {/* <div className='btn w150 mr10'>Delete</div> */}
          <div className={`btn w150 ${isSaving ? 'btndisabled' : ''}`} onClick={this.onSubmit}>{params.mid ? 'Update' : 'Save'}</div>
        </div>
      </div>
    );
  }
}

function mapStateToProps({ auth: { token }, page: { suggesions = {}, individualItem: { data = {} }, itemLoader = false } }) {
  return { token, itemData: data, itemLoader, suggesions }
}
const mapDispatchToProps = function (dispatch) {
  return {
    fetchSuggestions: myParam => dispatch(fetchSuggestions(myParam, 'suggesions')),
    updatePageData: (myParam, cb) => dispatch(updatePageData(myParam, cb)),
    getIndividualItem: (myParams) => dispatch(getIndividualItem(myParams)),
    resetPageStore: myParam => dispatch(resetPageStore(myParam))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Staffmember);