import React, { Component } from 'react';
import { connect } from 'react-redux';

import InputField from '../components/customFields/inputField';
import Chips from '../components/customFields/chips';
import Loader from '../components/customFields/loader';
import { getValue, dateToStr } from '../utils/formatter';
import { isObject, isObjEmpty } from '../utils';
import { validate } from '../utils/validation';
import { Endpoint } from '../utils/constants';
import { updatePageData, getIndividualItem, resetPageStore } from '../actions/page';

class InviteGuests extends Component {
    constructor(props) {
        super(props);
        this.state = this.intialiseState();
        //isSaving: false;
    }

    intialiseState = (data = {}) => {
        return {
            memberData: getValue(data, 'memberObj', ''),
            memberId: getValue(data, 'memberId', ''),
            date: getValue(data, 'date', null),
            startTime: getValue(data, 'startTime', null),
            nature: getValue(data, 'nature', ''),
            guests: getValue(data, 'guests', this.guest()),
            errorMessages: {},
            isValid: {},
            guestErr: {}
        }
    }

    componentDidMount() {
        const {match: {params: {id}}} = this.props;
        if (id) {
            this.setState({id});
            this.props.getIndividualItem({
                params: { token: this.props.token },
                url: `${Endpoint.CHECKIN_GUEST}/${id}`
            });
        }
    }

    componentDidUpdate(prevProps, prevState) {
        // if (this.state.id && prevProps.itemData != this.props.itemData && isObject(this.props.itemData) && !isObjEmpty(this.props.itemData)) {
        //     let stateD = this.intialiseState(this.props.itemData);
        //     this.setState(stateD);
        // }
    }

    guest = (n = 1) => {
        let r = [];
        while (r.length < n) {
            r = [...r, { name: '', phone: '', email: '', withGuest: '' }]
        }
        return r;
    }

    validate = (name, value) => {
        let { isValid, errorMessages } = this.state;
        let error = validate(name, value, { type: 'text' });
        isValid[name] = error.isValid;
        error.isValid
            ? delete errorMessages[name]
            : (errorMessages[name] = error.errorMsg);
        this.setState({ isValid, errorMessages });
    };

    inputChange = (e, id) => {
        let {
            target: { name, value, checked, type }
        } = e;
        if (id || id === 0) {
            let { guestErr, guests } = this.state;
            if (name == 'withGuest') {
                if (value < 0 || value > 4) {
                    return;
                }
            } else {
                let error = validate(name, value, { type: 'text', regexType: ['email', 'phone'].indexOf(name) > -1 ? name : 'noop' });
                guestErr[id] = guestErr[id] ? guestErr[id] : {}
                error.isValid ? delete guestErr[id][name]
                    : (guestErr[id][name] = error.errorMsg);
            }
            guests[id][name] = value;

            this.setState({ guestErr, guests })
        } else {
            this.setState({ [name]: type == 'checkbox' ? checked : value }, () => {
                this.validate(name, value);
            });
        }
    };

    onSubmit = (e) => {
        e.preventDefault();
        ["memberId", "date", "startTime", "nature"].forEach((name) => {
            this.validate(name, this.state[name]);
        });
        let { guestErr, guests } = this.state;
        var isError = false;
        guests.forEach((g, id) => {
            ['name', 'phone', 'email'].forEach((name) => {
                let value = g[name];
                let error = validate(name, value, { type: 'text', regexType: ['email', 'phone'].indexOf(name) > -1 ? name : 'noop' });
                guestErr[id] = guestErr[id] ? guestErr[id] : {}
                if (error.isValid) {
                    delete guestErr[id][name];
                } else {
                    guestErr[id][name] = error.errorMsg;
                    isError = true;
                }
            })
        })
        this.setState({ error: '', guestErr });
        if (Object.keys(this.state.errorMessages).length == 0 && !isError) {
            let payload = Object.assign({}, this.state);
            payload.token = this.props.token
            delete payload.errorMessages;
            delete payload.isValid;
            delete payload.guestErr;
            this.props.updatePageData({
                payload,
                url: `${Endpoint.GUEST_MANAGE}`,
                method: this.props.id ? "PUT" : 'POST',
                action: this.props.id ? "update" : 'add',
                //page: 'invite',
            }, (response) => {
                if (response.status) {
                    this.setState({ error: '' });
                    this.props.toastmessage(response.message);
                    this.props.history.push('/view-guest');
                } else {
                    this.setState({ error: response.message })
                }
            });
        }
        // this.setState({ isSaving: true });
    }
    setChips = (value, name, item = {}) => {
        this.setState({ [name]: value, memberId: value && value.length ? value[0].id : '' });
    };

    addGuest = (e, n) => {
        e.preventDefault();
        let l = this.state.guests.length;
        if (n == l) {
            return;
        }
        if (n < l) {
            this.setState({ guests: this.state.guests.filter((g, i) => { return i + 1 <= n }) });
        } else {
            this.setState({ guests: [...this.state.guests, ...this.guest(n - l)] });
        }
    }

    renderGusetCount = () =>{
        let r = [];
        for (let i = 0; i < 10; i++){
            r[i] = <span key={i} className={`${this.state.guests.length === i+1 ? 'active' : ''} btn`} onClick={(e) => this.addGuest(e, i+1)}>{i+1}</span>
        } 
        return r;
    }

    render() {
        let { memberId, memberData, startTime, date, nature, guests, isValid, errorMessages, guestErr, isSaving } = this.state;
        let { token } = this.props;
        return (
            <div className='inviteGuests'>
                <div className='_title'>INVITE A GUEST FOR A MEMBER</div>
                <div className='row no-gutters pt10'>
                    <div className='col-3'>
                        <div className='w90per'>
                            <div className='pr'>
                                <Chips
                                    placeholder="Member Name or Member No."
                                    className=""
                                    inputClassName="w100per search-input"
                                    name="memberData"
                                    namespace="countryList"
                                    livesearch={true}
                                    autocomplete={false}
                                    isSingle={true}
                                    service={`${Endpoint.MEMBER}?token=${token}`}
                                    vk="id"
                                    sk="name"
                                    image="image"
                                    stamp="mno"
                                    chips={memberData}
                                    isHighlight={true}
                                    maxCount={1}
                                    setChips={this.setChips}
                                />
                                {!memberId && <span className='search-icon'><i className='fa fa-search'></i></span>}
                            </div>
                            {memberId &&
                                <div className='guest-detail'>
                                    <div className='d-flex justify-content-between'>
                                        <p className='txt-member pr15'>{memberData[0].name}</p>
                                        <p className='fs14 member-no'>M No. - {memberData[0].mno}</p>
                                    </div>
                                    <div className='row no-gutters mt20'>
                                        <div className='col'>
                                            <div className='pr10'>
                                                <p>Date</p>
                                                <InputField name="date" groupClass="date-input" value={date} isValid={isValid.date} errorMsg={errorMessages.date}
                                                    inputChange={this.inputChange} dom={{ placeholder: 'Date', type: 'date', minDate: new Date() }} />
                                            </div>
                                            <div className='mt5 pr10'>
                                                <p>Time</p>
                                                <InputField name="startTime" groupClass="date-input" value={startTime} isValid={isValid.startTime} errorMsg={errorMessages.startTime}
                                                    inputChange={this.inputChange} dom={{ placeholder: 'Time', type: 'time' }} />
                                            </div>
                                        </div>
                                        <div className='col-5 text-right'>
                                            <p>No. of Guests</p>
                                            {this.renderGusetCount()}
                                            {/* <span className='btn' onClick={(e) => this.addGuest(e, 1)}>1</span>
                                            <span className='btn' onClick={(e) => this.addGuest(e, 2)}>2</span>
                                            <span className='btn' onClick={(e) => this.addGuest(e, 3)}>3</span>
                                            <span className='btn' onClick={(e) => this.addGuest(e, 4)}>4</span> */}
                                        </div>
                                    </div>
                                    <div>
                                        <InputField name="nature" value={nature} isValid={isValid.nature} errorMsg={errorMessages.nature}
                                            inputChange={this.inputChange} dom={{ placeholder: 'NATURE OF INVITE' }} />
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                    {memberId &&
                        <div className='col-9'>
                            <div className='guest-member'>
                                {guests.map((guest, i) => {
                                    let err = guestErr[i] ? guestErr[i] : {}
                                    return (
                                        <div className='row no-gutters' key={i}>
                                            <div className='col-1 color-white num'>{i + 1}.</div>
                                            <div className='col-5'>
                                                <div className='pr15'>
                                                    <InputField name="name" groupClass="member-detail" value={guest.name} isValid={err.name ? false : true} errorMsg={err.name}
                                                        inputChange={(e) => this.inputChange(e, i)} dom={{ placeholder: 'Name' }} />
                                                </div>
                                            </div>
                                            <div className='col-3'>
                                                <div className='pr15'>
                                                    <InputField name="phone" groupClass="member-detail numberArrow" value={guest.phone} isValid={err.phone ? false : true} errorMsg={err.phone}
                                                        inputChange={(e) => this.inputChange(e, i)} dom={{ placeholder: 'Phone No.', type: 'number' }} />
                                                </div>
                                            </div>
                                            <div className='col-2'>
                                                <div className='pr15'>
                                                    <InputField name="withGuest" groupClass="member-detail text-center numberArrow" value={guest.withGuest} isValid={err.withGuest ? false : true} errorMsg={err.withGuest}
                                                        inputChange={(e) => this.inputChange(e, i)} dom={{ placeholder: '+0', type: 'number' }} />
                                                </div>
                                            </div>
                                            <div className='col-10 email'>
                                                <div className='pr15'>
                                                    <InputField name="email" groupClass="member-detail" value={guest.email} isValid={err.email ? false : true} errorMsg={err.email}
                                                        inputChange={(e) => this.inputChange(e, i)} dom={{ placeholder: 'Email', type: 'email' }} />
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>
                        </div>
                    }
                </div>
                {memberId &&
                    <div className='row mt30'>
                        <div className='col-10 text-right' onClick={this.onSubmit}><span className={`btn send-btn mt15 ${isSaving ? 'btndisabled' : ''}`}>SEND INVITE TO GUEST</span></div>
                    </div>
                }
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: { individualItem: { data = {} }, itemLoader = false } }) {
    return { token, itemData: data, itemLoader }
}

export default connect(mapStateToProps, { updatePageData, getIndividualItem, resetPageStore })(InviteGuests);
