import React, { Component } from 'react';
import { connect } from 'react-redux';

import { getPageData, resetPageStore, updatePageData } from '../actions/page';
import { Endpoint } from '../utils/constants';
import Loader from '../components/customFields/loader';

class Roles extends Component {

    constructor(props) {
        super(props);
        this.state = {
            slectedData: {},
            isInit: true,
            error: ''
        };
    }

    componentDidMount() {
        this.props.getPageData({
            params: { token: this.props.token, type: 'roleManage' },
            url: Endpoint.MODULE_MANAGE
        })
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.moduleList != prevProps.moduleList && this.props.moduleList.user && this.props.moduleList.user.length && prevState.isInit) {
            let slectedData = this.state.slectedData;
            this.props.moduleList.user.forEach((item) => {
                slectedData[item.id] = item.moduleIds && typeof item.moduleIds === 'string' ? item.moduleIds.split(',') : []
            })
            this.setState({ slectedData, isInit: false });
        }
    }

    componentWillUnmount() {
        this.props.resetPageStore('pageData');
    }

    inputChange = e => {
        let { target: { value, dataset } } = e;
        let { slectedData } = this.state;
        slectedData[dataset.user] = slectedData[dataset.user] ? slectedData[dataset.user] : []
        let i = slectedData[dataset.user].indexOf(value);
        if (i > -1) {
            slectedData[dataset.user].splice(i, 1);
        } else {
            slectedData[dataset.user] = [...slectedData[dataset.user], value];
        }
        this.setState({ slectedData });
    };

    selectAll = (e) => {
        let { target: { dataset } } = e;
        let { slectedData } = this.state;
        let { moduleList: { modules = [] } } = this.props;
        slectedData[dataset.user] = slectedData[dataset.user] ? slectedData[dataset.user] : []
        if (slectedData[dataset.user].length) {
            slectedData[dataset.user] = []
        } else {
            modules.forEach((m, i) => {
                if (slectedData[dataset.user].indexOf(m.id) === -1) {
                    slectedData[dataset.user] = [...slectedData[dataset.user], "" + m.id];
                }
            })
        }
        this.setState({ slectedData });
    }

    onSubmit = (e) => {
        e.preventDefault();
        this.setState({ error: '', isSaving: true });
        this.props.updatePageData({
            payload: { moduleIds: this.state.slectedData, token: this.props.token },
            url: Endpoint.MODULE_MANAGE,
            method: 'POST',
        }, (response) => {
            if (response.status) {
                this.setState({ error: '', isSaving: false });
                this.props.history.push('/member');
                this.props.toastmessage(response.message);
            } else {
                this.setState({ error: response.message, isSaving: false })
            }
        });
    }

    render() {
        let { isLoader, moduleList: { modules = [], user = [] } } = this.props;
        let { slectedData, error, isSaving } = this.state;
        if (isLoader) { return <Loader /> }
        return (
            <div className='roles'>
                <table className="table table-bordered ">
                    <tbody><tr>
                        <td>User List/Module List</td>
                        {modules.map((m, i) => {
                            return (
                                <td className='pr' key={i} style={{ paddingBottom: '45px' }}>{m.name}
                                    <div className='d-flex justify-content-center pa w100per color-white' style={{ left: '0px', bottom: '0px', padding: '10px', borderTop: '1px solid #333' }}>
                                        {/* <p>View</p><p>|</p> */}
                                        <p>Edit</p>
                                    </div>
                                </td>
                            )
                        })}
                    </tr>
                        {user.map((u, idx) => {
                            slectedData[u.id] = slectedData[u.id] ? slectedData[u.id] : [];
                            let indeterminate = slectedData[u.id].length && slectedData[u.id].length < modules.length;
                            return (<tr key={'user_' + idx}>
                                <td width="15%" className='user-name'>{u.name} <input type="checkbox" name={'all_' + u.id} checked={slectedData[u.id].length >= modules.length}
                                    indeterminate={indeterminate.toString()} data-user={u.id} onChange={this.selectAll} />
                                </td>
                                {modules.map((m, i) => {
                                    return <td width="10%" key={i}>
                                        <div className='d-flex justify-content-center'>
                                            {/* <input type="checkbox" checked={slectedData[u.id].indexOf("" + m.id) > -1}
                                                value={m.id} name={m.slug} data-user={u.id} onChange={this.inputChange} /> */}
                                            <input type="checkbox" checked={slectedData[u.id].indexOf("" + m.id) > -1}
                                                value={m.id} name={m.slug} data-user={u.id} onChange={this.inputChange} />
                                        </div>
                                    </td>
                                })}
                            </tr>)
                        })}
                    </tbody>
                </table>
                <p className='err_msg'>{error}</p>
                <div className='text-center'>
                    <div className={`btn w150 ${isSaving ? 'btndisabled' : ''}`} onClick={this.onSubmit}>save</div>
                </div>

            </div>
        )
    }
}
function mapStateToProps({ auth: { token }, page: { pageData: { data = {}, count = 0 }, updatedData = {}, isLoader = false } }) {
    return { token, isLoader, moduleList: data, count, updatedData }
}
export default connect(mapStateToProps, { getPageData, resetPageStore, updatePageData })(Roles);