import React, { Component } from 'react';
import { connect } from 'react-redux';

import ModalButton from '../components/modal/button';
import { getPageData, resetPageStore, fetchSuggestions } from '../actions/page';
import { Endpoint } from '../utils/constants';
import { isObjEmpty } from '../utils';
import Loader from '../components/customFields/loader';
import NoRecord from '../components/customFields/noRecord';

class GlobalAccess extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isinit: true,
            list: [],
            totalCount: '',
            selected: '',
            selectedItem: {},
        };
    }

    componentDidMount() {
        this.props.fetchSuggestions({ url: `${Endpoint.LOCATION}?type=continent&token=${this.props.token}`, type: 'combo' });
    }

    componentWillUnmount() {
        this.props.resetPageStore('pageData');
    }

    getList = (id) => {
        this.props.getPageData({
            params: { token: this.props.token, cid: id },
            url: Endpoint.GLOBAL_ACCESS
        })
    }

    filterItem = (list, item) =>{
       let f  = list.filter((c, i)=>{
           return  item.countryId === c.countryId;
        })
        return f.length ? f[0] : list[0];
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.continentList != prevProps.continentList && !isObjEmpty(this.props.continentList) && prevState.isinit) {
            let i = Object.keys(this.props.continentList)[0];
            this.setState({ selected: i, isinit: false });
            this.getList(i);
        }

        if (this.props.list != prevProps.list) {
            this.setState({ list: this.props.list, totalCount: this.props.count }, () => {
                if (this.state.list.length) {
                    let item = !Object.keys(this.state.selectedItem).length ? this.state.list[0] : this.filterItem(this.state.list, this.state.selectedItem);
                    this.selectCity(item, true);
                }
            })
        }

        if (this.props.updatedData != prevProps.updatedData && !isObjEmpty(this.props.updatedData) && this.props.updatedData.page === 'ga') {
            let { action, data } = this.props.updatedData;
            this.getList(this.state.selected);
            this.props.resetPageStore('updatedData')
        }

    }

    setect = (cid) => {
        this.setState({ selected: cid, selectedItem: {} }, () => {
            this.getList(this.state.selected);
        });

    }

    selectCity = (item, isReload = false) => {
        if (item.countryId === this.state.selectedItem.countryId && !isReload) {
            return;
        }
        this.setState({ selectedItem: item });
    }

    render() {
        let { list, selected, selectedItem } = this.state;
        let { isLoader, continentList } = this.props;
        return (
            <div className='global-access quorum_new'>
                <div className='d-flex justify-content-between mb20'>
                    <div className='title-txt'>GLOBAL ACCESS</div>
                    <div className=''>
                        <ModalButton
                            className="btn w150"
                            data={{
                                showCustomModal: true,
                                title: '',
                                path: 'globalAccess/globalAccess',
                                body: '',
                                className: 'modal-extra-large'
                            }}
                        >
                            Add new <span className='ml5 fs18'><i className="fa fa-plus-circle"></i></span>
                        </ModalButton>
                    </div>
                </div>
                <div className='row no-gutters'>
                    <div className='col-4'>
                        <div className='scroll-global'>
                            <div className='d-flex flex-wrap'>
                                {continentList && Object.keys(continentList).length > 0 && Object.keys(continentList).map((c, i) => {
                                    return <span className={`tabs tab-square ${selected == c ? 'active' : ''} ${i % 2 === 0 ? 'mr15' : ''}`} key={c} onClick={() => this.setect(c)}>{continentList[c]}</span>
                                })}
                            </div>
                        </div>
                    </div>
                    <div className='col-3'>
                        <div className='scroll-global'>
                            {isLoader && <Loader />}
                            {!isLoader && (!list || list.length == 0) && <NoRecord />}
                            <ul className='cityList'>
                                {!isLoader && list && list.length > 0 && list.map((item, i) => {
                                    return (
                                        <li className={`cityListItem ${selectedItem.countryId == item.countryId ? 'active' : ''}`} key={item.countryId} onClick={() => this.selectCity(item)}><p>{item.countryName}</p><span><i className={'fa fa-chevron-right '}></i></span></li>
                                    )
                                })}
                            </ul>
                        </div>
                    </div>
                    <div className='col'>
                        <div className='scroll-global'>
                            <div className='clubList d-flex flex-wrap'>
                                {selectedItem && selectedItem.clubs && selectedItem.clubs.length > 0 && selectedItem.clubs.map((item) => {
                                    return (
                                        <a className='listItme' key={item.id}>
                                            <ModalButton
                                                className="ico-Box"
                                                data={{
                                                    showCustomModal: true,
                                                    title: '',
                                                    id: item.id,
                                                    path: 'globalAccess/globalAccess',
                                                    body: '',
                                                    className: 'modal-extra-large'
                                                }}
                                            >
                                                <div className='image-style' style={{ backgroundImage: `url(${item.logo1 || item.logo2})` }}>
                                                    <img src={'./images/imagesize1_1.png'} className='w100per' />
                                                </div>
                                                <p className='clubName text-uppercase color-white pt5'>{item.clubName}</p>
                                                <p className='cityName text-uppercase color-gray'>{item.cityName}</p>
                                            </ModalButton>

                                        </a>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = function (dispatch, ownProps) {
    return {
        fetchSuggestions: myParam => dispatch(fetchSuggestions(myParam, 'continentList')),
        getPageData: myParam => dispatch(getPageData(myParam)),
        resetPageStore: myParam => dispatch(resetPageStore(myParam))
    };
};
function mapStateToProps({ auth: { token }, page: { continentList = {}, pageData: { data = [], count = 0 }, updatedData = {}, isLoader = false } }) {
    return { token, isLoader, list: data, count, updatedData, continentList }
}
export default connect(mapStateToProps, mapDispatchToProps)(GlobalAccess);
