import React, { Component } from 'react';
import { connect } from 'react-redux';

import InputField from '../customFields/inputField';
import Chips from '../customFields/chips';
import { getValue, dateToStr } from '../../utils/formatter';
import { Endpoint } from '../../utils/constants';
import '../../style/newStyle.scss';

class Filters extends Component {
  constructor(props) {
    super(props);
    this.state = this.intialiseState();
  }
  intialiseState = (data = {}) => {
    return {
      memberId: getValue(data, 'memberId', ''),
      keyword: getValue(data, 'keyword', ''),
      chips: getValue(data, 'chips', ''),
      date: getValue(data, 'date', dateToStr(null, 'YYYY-MM-DD')),
      rTypeId: getValue(data, 'rTypeId', ''),
    }
  }

  inputChange = e => {
    let {
      target: { name, value }
    } = e;
    this.setState({ [name]: value });
  };
  search = e => {
    e.preventDefault();
    const { rTypeId, date, memberId, keyword } = this.state;
    this.props.refreshList(rTypeId, date, { memberId, keyword });
  };

  setChips = (value, name, item = {}) => {
    this.setState({ memberId: item.id || '', chips: value, keyword: '' });
  };

  setKeyword = (keyword) => {
    this.setState({ keyword });
  }
  render() {
    const { chips, rTypeId, date } = this.state;
    const { token, suggesions } = this.props;
    return (
      <div className="col-md-6">
        <div className="row no-gutters">
          <div className="col-md-4">
            <div className="tabs active">
              <p className="">Filters</p>
            </div>
          </div>
          <div className="col text-right">
            <div className="backBtn" onClick={this.props.goBack}>
              Back
                    </div>
          </div>
        </div>
        <div className="checkedin_data">
          <div className="row">
            <div className="col-6">
              <p>Date</p>
              <InputField
                name="date"
                groupClass="mr10"
                value={date}
                inputChange={this.inputChange}
                dom={{
                  placeholder: "dd/mm/yyyy",
                  type: "date",
                  //maxDate: new Date(),
                  dateFormat: "dd/MM/yyyy"
                }}
              />
            </div>
            <div className="col-6">
              <p>Room</p>
              <InputField name="rTypeId" value={rTypeId}
                inputChange={this.inputChange}
                options={suggesions}
                dom={{ placeholder: 'All Category', type: 'select' }} />
            </div>
          </div>
          <div>
            <p>Member Details</p>
            <Chips
              className="checkIn-search"
              placeholder="Search by First Name, Last Name or Member ID"
              name="memberId"
              namespace="countryList"
              livesearch={true}
              autocomplete={false}
              isSingle={true}
              service={`${Endpoint.MEMBER}?token=${token}`}
              vk="id"
              sk="name"
              chips={chips}
              maxCount={1}
              image="image"
              stamp="mno"
              isHighlight={true}
              setChips={this.setChips}
              onInputChage={this.setKeyword}
            />
          </div>
          <div className="text-right">
            <div onClick={this.search} className={`btn`}>
              Search
                </div>
          </div>
        </div>
      </div>

    );
  }
}
function mapStateToProps({ auth: { token }, page: { suggesions = {} } }) {
  return { token, suggesions }
}
export default connect(mapStateToProps)(Filters);
