import React, { Component } from 'react';

import { connect } from 'react-redux';

import ModalButton from '../components/modal/button';
import { getPageData, fetchSuggestions, resetPageStore } from '../actions/page';
import { Endpoint } from '../utils/constants';
import { isObjEmpty } from '../utils';
import Loader from '../components/customFields/loader';
import InputField from '../components/customFields/inputField';
import NoRecord from '../components/customFields/noRecord';


class Qspecial extends Component {
    constructor(props) {
        super(props);
        this.state = {
            category: '',
            isinit: true,
            list: [],
            totalCount: ''
        };
    }
    componentDidMount() {
        this.props.fetchSuggestions({ url: Endpoint.QSPECIAL_CATEGORIES, params: { token: this.props.token }, type: 'combo', value: 'category' });
        this.getList();
    }

    getList = () => {
        this.props.getPageData({
            params: { token: this.props.token, cat: this.state.category },
            url: Endpoint.QSPECIAL_MANAGE
        })
    }

    componentWillUnmount() {
        this.props.resetPageStore('pageData');
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.list != prevState.list && prevState.isinit) {
            return { list: nextProps.list, totalCount: nextProps.count, isinit: false }
        }
        return null
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.updatedData != prevProps.updatedData && !isObjEmpty(this.props.updatedData) && this.props.updatedData.page == 'qspecial') {
            let { action, data } = this.props.updatedData;
            console.log(action, data);
            if (action === 'update') {
                let list = this.state.list.map((item) => {
                    if (item.id == data.id) {
                        return data;
                    }
                    return item;
                })
                this.setState({ list });
            } else if (action === 'add') {
                this.setState({ list: [data, ...this.state.list] })
            } else if (action === 'delete') {
                this.setState({ list: this.state.list.filter(item => { return item.id != data.id }) })
            }
            this.props.resetPageStore('updatedData')
        }
        if (prevProps.list != this.props.list) {
            this.setState({ list: this.props.list, totalCount: this.props.count });
        }
    }

    onSelect = (e) => {
        let { target: { name, value } } = e;
        this.setState({ [name]: value, isinit: true }, () => {
            //this.props.resetPageStore('pageData');
            this.getList();
        })
    }
    render() {
        let { list, category } = this.state;
        let { isLoader, qsCatList } = this.props;
        return (
            <div className='events q_special member'>
                <div className='row mb15'>
                    <div className='col-5'><div className='txt-title'>New at the Q</div></div>
                    <div className='col-2'>
                        <div className="select-casting select-catgry pr d-flex align-items-center cursor-default">
                            <InputField className="category text-truncate text-uppercase color-white" name="category" value={category}
                                inputChange={this.onSelect} options={qsCatList} dom={{ placeholder: 'By Category', type: 'select' }} />
                        </div>
                    </div>
                    <div className='col-5 text-right'>
                        <ModalButton
                            className="ico-Box"
                            data={{
                                showCustomModal: true,
                                title: '',
                                path: 'Qspecial/Qspecial',
                                body: '',
                                className: 'modal-large'
                            }}
                        >
                            <div className='btn w150'>Add new <span className='ml5 fs18'><i className="fa fa-plus-circle"></i></span></div>
                        </ModalButton>

                    </div>
                </div>
                {isLoader && <Loader />}
                {!isLoader && (!list || list.length === 0) && <NoRecord />}

                <div className='mt30 list-scroll'>
                    <div className='d-flex flex-wrap'>
                        {!isLoader && list && list.length > 0 &&
                            list.map((item, i) => {
                                return (
                                    <div href='#' className='list-item' key={i}>
                                            <div className='flex-column'>
                                                <div className='image-style' style={{ backgroundImage: `url(${item.photo})` }}>
                                                    <img src={'./images/imagesize2_3.png'} className='w100per' alt='b'/>
                                                </div>
                                                <ModalButton
                                                    className="ico-Box"
                                                    data={{
                                                        showCustomModal: true,
                                                        id: item.id,
                                                        title: '',
                                                        path: 'Qspecial/Qspecial',
                                                        body: '',
                                                        className: 'modal-large'
                                                    }}
                                                >
                                                    <div className='btn w100per mt10 fs14'>EDIT</div>
                                                </ModalButton>

                                            </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>

            </div >
        );
    }
}

const mapDispatchToProps = function (dispatch, ownProps) {
    return {
        fetchSuggestions: myParam => dispatch(fetchSuggestions(myParam, 'qsCatList')),
        getPageData: myParam => dispatch(getPageData(myParam)),
        resetPageStore: myParam => dispatch(resetPageStore(myParam))
    };
};
function mapStateToProps({ auth: { token }, page: { pageData: { data = [], count = 0 }, updatedData = {}, isLoader = false, qsCatList = {} } }) {
    return { token, isLoader, list: data, count, updatedData, qsCatList }
}
export default connect(mapStateToProps, mapDispatchToProps)(Qspecial);

